import { Component, OnInit } from '@angular/core';
//import { config_logo,config_company_name } from '../../../assets/clientConfig/clientConfig';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment.prod';

@Component({
  selector: 'app-registered-page',
  templateUrl: './registered-page.component.html',
  styleUrls: ['./registered-page.component.css']
})
export class RegisteredPageComponent implements OnInit {
 config:any={};
 config_company_name:string;
  logo_url:string;
  constructor(private http:HttpClient) { }
  
  ngOnInit(): void {
   // this.config_company_name=config_company_name;
	//this.logo_url=config_logo;
  this.http.get(environment.api_Url+'getConfig/').subscribe(data => {
    // this.config=(data);
     this.config=data;
     console.log(this.config);
     this.config.Logo=(data[0].Logo);
     this.logo_url=environment.api_Url+this.config.Logo;
    this.config_company_name=(data[0].CompanyName);
  });
  }

}
