 import { Component, OnInit } from '@angular/core';
 import { DetailsService } from 'src/app/Service/details.service';
 import { ActivatedRoute,Router } from '@angular/router';
 import { HttpClient } from '@angular/common/http';
 import {CommonModule} from '@angular/common';
 import { NgForm } from '@angular/forms';
 import { LocalStorageService } from 'ngx-webstorage';
 import { environment } from 'src/environments/environment.prod';
 import * as XLSX from 'xlsx';
 import { NgxUiLoaderService } from 'ngx-ui-loader';

@Component({
  selector: 'app-metrics',
  templateUrl: './metrics.component.html',
  styleUrls: ['./metrics.component.css']
})
export class MetricsComponent implements OnInit {
  user;
  userid:any;
  userroleno:any;
  userdetails:any;
  vendor:any;
  hrrole:boolean;
  vendorrole:boolean;
  adminrole:boolean;
  managerrole:boolean;
  startdate:any;
  enddate:any;
  Metrics:any;
  message:any;
  nodata:any;
  status:boolean;
  prevdate:any;
  date: Date = new Date();
  title30:boolean;
  title:boolean;
  httpheaders:string;
  cp: number = 1;
  paginateflag:Boolean;
  fileName= 'Report_Metrics.xlsx';
    constructor(private http:HttpClient,private route:ActivatedRoute,private router:Router,private service:DetailsService,private localSt:LocalStorageService,private ngxService: NgxUiLoaderService) { }
  
    ngOnInit(): void {
  
      this.user=this.localSt.retrieve('user');
       this.userid=this.user[0].userid;
       this.userroleno=this.user[0].userrole;
       const idToken = this.user[1].accesstoken;
       this.httpheaders="Bearer " + idToken;
  
       this.service.getuserdetailsbyid(this.userid,this.userroleno).subscribe((result)=>{
          this.userdetails=result;
          console.warn("username:",this.userdetails);
   
        });
       this.checkuserrole(this.userroleno);
       this.prevdate=new  Date(Date.now() - 30 * 24 * 60 * 60 * 1000)
       this.startdate= this.prevdate.toISOString().substr(0, 10);
       this.enddate=this.date.toISOString().substr(0, 10);
       console.log(this.startdate);
       console.log(this.enddate);
       console.log(this.userroleno);
       this.title30=true;
       this.title=false;
       this.ngxService.start();
       /***Init page loader */
        const initload = new FormData();
        console.log(this.startdate);
        initload.append('startdate',this.startdate);
        initload.append('enddate', this.enddate);
        //load the report for vendor, admin,management
        if(this.userroleno==2||this.userroleno==1)
        { 
             this.http.post(environment.api_Url+'adminmetricsreport',initload,{
               headers:{"Authorization":this.httpheaders}
              }).subscribe(data => {
                this.ngxService.stop();
                this.Metrics=data;
                 this.showMetrics();
          });
        }else if(this.userroleno==4)
        {
          this.http.get(environment.api_Url+'viewvendorid/' +this.userid,{
            headers:{"Authorization":this.httpheaders}
           }).subscribe(data=>{
              this.ngxService.stop();
              this.vendor=data;
              this.vendor[0].vid=(data[0].VendorID);
  
              this.http.post(environment.api_Url+'vendormetricsreport/'+this.vendor[0].vid,initload,{
                headers:{"Authorization":this.httpheaders}
                }).subscribe(data => {
                      this.ngxService.stop();
                      this.Metrics=data;
                      this.showMetrics(); 
           
          });
        });
        }
         else 
         {  //load the report for HR -his own published JD 
             this.http.post(environment.api_Url+'hrmetricsreport/'+this.userid,initload,{
             headers:{"Authorization":this.httpheaders}
            }).subscribe(data => {
              this.ngxService.stop();
               this.Metrics=data;
             this.showMetrics();
              });
         }
  
    }
    resumeupload(id:any){   /*JDID**userid**userroleid*/
      this.router.navigate(['/ResumeUpload',id]);
      console.warn(this.userroleno);
    }
    checkuserrole(id:any)
    {
      if(id==3)
     {
       this.hrrole=true
      }
      else if(id==4)
      {
        this.vendorrole=true
      }
      else if(id==2)
      {
        this.managerrole=true
      }
      else if(id==1)
      {
        this.adminrole=true
      }
   }
   collectData(form:NgForm)
   {
    this.title30=false;
    this.title=true;
    this.ngxService.start();
    if(this.userroleno==2||this.userroleno==1)
        { 
          
             this.http.post(environment.api_Url+'adminmetricsreport',form.value,{
               headers:{"Authorization":this.httpheaders}
              }).subscribe(data => {
                this.ngxService.stop();
                this.Metrics=data;
                this.showMetrics();
          },error=>
          {
            console.log(error);
          });
        }else if(this.userroleno==4)
        {
          this.http.get(environment.api_Url+'viewvendorid/' +this.userid,{
            headers:{"Authorization":this.httpheaders}
           }).subscribe(data=>{
              this.vendor=data;
              console.log(data);
              console.log(data[0].VendorID);
              this.vendor[0].vid=(data[0].VendorID);
              console.log("this.vendor[0]"+this.vendor[0].vid);       
              this.http.post(environment.api_Url+'vendormetricsreport/'+this.vendor[0].vid,form.value,{
                headers:{"Authorization":this.httpheaders}
                }).subscribe(data => {
                      this.ngxService.stop();
                      this.Metrics=data;
                      this.showMetrics();
           
          });
        });
        }
         else 
         {  //load the report for HR -his own published JD 
             this.http.post(environment.api_Url+'hrmetricsreport/'+this.userid,form.value,{
             headers:{"Authorization":this.httpheaders}
            }).subscribe(data => {
              this.ngxService.stop();
              this.Metrics=data;
              this.showMetrics();
            
            });
       }

 }


 showMetrics()
{

    if(this.Metrics.length==0)
    {
      console.log("here");
      this.status=true;
      this.nodata="No Data available betweeen this dates";
    }
    else{
      this.status=false;
     this.message=true;
    for(var i=0;i<Object.keys(this.Metrics).length;i++)
    {
      if(this.Metrics[i].vrt ==0) this.Metrics[i].vrt=0.5;
      if(this.Metrics[i].mvrt ==0) this.Metrics[i].mvrt=0.5;
      if(this.Metrics[i].hrt ==0) this.Metrics[i].hrt=0.5;
      if(this.Metrics[i].mhrt ==0) this.Metrics[i].mhrt=0.5;
      if(this.Metrics[i].crt ==0) this.Metrics[i].crt=0.5;
      if(this.Metrics[i].mcrt ==0) this.Metrics[i].mcrt=0.5;
      if(this.Metrics[i].vcost ==0) this.Metrics[i].vcost=0.5;
      if(this.Metrics[i].hcost ==0) this.Metrics[i].hcost=0.5;
    }
   if (Object.keys(this.Metrics).length > 10)
                {this.paginateflag=true;}
                  else { this.paginateflag=false;}
               }
  
   }

  //function to download the report as excel  
 exportexcel()
 {
  /* pass here the table id */
  let element = document.getElementById('report-table');
  const ws: XLSX.WorkSheet =XLSX.utils.table_to_sheet(element);
 
  /* generate workbook and add the worksheet */
  const wb: XLSX.WorkBook = XLSX.utils.book_new();
  XLSX.utils.book_append_sheet(wb, ws, "Sheet1");
 
  /* save to file */  
  XLSX.writeFile(wb, this.fileName);
 }

}
