import { Injectable } from '@angular/core';
import {
  HttpRequest,
  HttpHandler,
  HttpEvent,
  HttpInterceptor,
  HttpErrorResponse
} from '@angular/common/http';
import { Observable,throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { NotificationService } from 'src/app/Service/notification.service';

@Injectable()
export class HttperrorInterceptor implements HttpInterceptor {
  message:string;
  constructor(private notifyService : NotificationService) {}

  intercept(request: HttpRequest<unknown>, next: HttpHandler): Observable<HttpEvent<unknown>> {
    console.log("HTTP Interceptor Started")
    return next.handle(request)
      .pipe(
        catchError((error:HttpErrorResponse)=>{
          this.message=error.error.messageText;
         if(this.message)
          {
           // this.notifyService.showError(this.message, "TALNET");
          }
          else
            this.notifyService.showError(error.error, "TALNET");
            return throwError(error.error.messageText);
        })
      );
  }
}
