  ngOnInit(): void {
    //fetching user information from local storage
    this.user=this.localSt.retrieve('user');
    this.userid=this.user[0].userid
    this.userroleno=this.user[0].userrole;
    this.resumeid=this.route.snapshot.params['id1'];
    this.jdid=this.route.snapshot.params['id2'];
    this.vendorname=this.route.snapshot.params['id3'];
    const idToken = this.user[1].accesstoken;
    this.httpheaders="Bearer " + idToken;
    this.display=true;
    this.show=false;
    this.from="info@talnetglobal.online";
    this.send_date="";
    this.cc="";
    this.ResumeFile="";
    this.minimumDate= moment().add(1,'day').format("YYYY-MM-DD");
    // fetching user details for given userid and role
    this.service.getuserdetailsbyid(this.userid,this.userroleno).subscribe((result)=>{
      this.userdetails=result;
      console.warn(result);
      this.con_message=false;
     });
     //fetching the resumes of a particular jd
     this.getresumedata(this.jdid,this.resumeid);
           
  }

