/***function to collect and add jd add from form  ****/  
uploadjddata(form:NgForm)
{
  // if(moment(this.postedon).isBefore(this.validtill))
   //{
    //console.log(true);
    
  if(parseInt(this.exp_from)<=parseInt(this.exp_to)) 
  {
  if(this.selectedFile== null)
  {
    this.addjdbtn.nativeElement.disabled="true";
    this.http.post(environment.api_Url+'AddJD',form.value,{
      headers:{"Authorization":this.httpheaders}
     }).subscribe(response=> 
     {
      if (response) 
      {
        this.message = 'Added successfully';
        this.notifyService.showSuccess("JD is succesfully created", "TALNET");
        this.router.navigate(['/Dashboard']);
      } 
      else 
      {
        this.message = 'Error';
        this.notifyService.showError("Unable to add the JD due to exception. Please try again.", "TALNET");
      }      
   });
  }
    else
    {

      var ext = this.selectedFile[0].name.substr(this.selectedFile[0].name.lastIndexOf('.')+1);
      if(this.validFormats.indexOf(ext) == -1){
        this.notifyService.showError("File can be only pdf / doc/ docx type", "TALNET");
     }else if (this.filesize <= 500000)
     {
      // this.posteddate=this.postedon;
       this.validtilldate=this.validtill;
      const uploadjdData = new FormData();
      uploadjdData.append('category', this.category);
      uploadjdData.append('jobtitle', this.jobtitle);
      uploadjdData.append('company', this.company);
      uploadjdData.append('bunit', this.bunit);
      uploadjdData.append('desc', this.desc);
      uploadjdData.append('skillset', this.skillset);
      uploadjdData.append('education', this.education);
      uploadjdData.append('location', this.location);
      uploadjdData.append('mandareq', this.mandareq);
      uploadjdData.append('nicetohave', this.nicetohave);
	   uploadjdData.append('exp_from', this.exp_from);
      uploadjdData.append('exp_to', this.exp_to);
      uploadjdData.append('certi', this.certi);
      uploadjdData.append('positions', this.positions);
      uploadjdData.append('postype', this.postype);
      uploadjdData.append('emptype', this.emptype);
            uploadjdData.append('summary', this.summary);
      uploadjdData.append('Terms', this.Terms);
     uploadjdData.append('visibility',this.visibilityflag);
      //uploadjdData.append('postedon', this.posteddate);
     uploadjdData.append('validtill', this.validtilldate);

      uploadjdData.append('createdby', this.userid);
      uploadjdData.append('hrexecid', this.hrexecid);
      uploadjdData.append('jdDoc', this.selectedFile[0], this.selectedFile[0].name);
      if(this.visibilityflag==0)
      {
       // uploadjdData.append('selectedvendorslist',JSON.stringify(this.dropDownForm.value));
       this.vendorlistselected = this.dropDownForm.value.myItems.map(v => v.VendorID);
       console.log(this.vendorlistselected);
       uploadjdData.append('selectedvendorslist',this.vendorlistselected);
      }
     
      console.log("The vendors selected are"+this.selectedItems);
      console.log("Load JD data:"+this.jdDoc);
      this.addjdbtn.nativeElement.disabled="true";
      this.http.post(environment.api_Url+'AddJDdoc',uploadjdData,{
        headers:{"Authorization":this.httpheaders}
      }).subscribe(response=> 
          {
            if (response) 
        {
          //this.message = 'Added successfully';
          this.notifyService.showSuccess("JD is succesfully created", "TALNET");
          this.router.navigate(['/Dashboard']);
        } 
        else 
        {
          //this.message = 'Error';
          this.notifyService.showError("Unable to add the JD due to exception. Please try again.", "TALNET");
        }      
          });
     }
     else {
      this.notifyService.showError("File should be less than 500KB.", "TALNET");
      }
  }
//}
//else{
  //this.notifyService.showError("Kindly Check the Valid till date", "TALNET");
//}
  }
  else{
    this.notifyService.showError("Experience-From range is more than To range ", "TALNET");
  }
}
//function to open the modal
open(content) {
  //  this.modalReference = this.modalService.open(content);
    this.modalService.open(content, { ariaLabelledBy: 'modal-basic-title' }).result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
  }

//function to dismiss the modal
  private getDismissReason(reason: any): string {
   
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return `with: ${reason}`;
    }
  }
  clearselect()
  {
	  this.dropDownForm.reset();
  }
