import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { LocalStorageService } from 'ngx-webstorage';
import { environment } from 'src/environments/environment.prod';
//import { config_logo } from '../../../assets/clientConfig/clientConfig';
import { NgxUiLoaderService } from 'ngx-ui-loader';

@Component({
  selector: 'app-forgot-password',
  templateUrl: './forgot-password.component.html',
  styleUrls: ['./forgot-password.component.css']
})
export class ForgotpasswordComponent implements OnInit {

  constructor(private http:HttpClient,private router:Router,private localSt:LocalStorageService,private ngxService: NgxUiLoaderService ) { }
  email:any;
  message:any;
  user:any;
  httpheaders;
  logo_url:string;
  config:any={};
  ngOnInit(): void {
	  //this.logo_url=config_logo;
    this.http.get(environment.api_Url+'getConfig/').subscribe(data => {
      // this.config=(data);
       this.config=data;
       console.log(this.config);
       this.config.Logo=(data[0].Logo);
       this.logo_url=environment.api_Url+this.config.Logo;
    });
    
  }

  //function to send password reset email
  forgot()
  {
      console.log(this.email);
//	var url=environment.api_Url;//-->url to run in local,below code for production url
//url=url.concat("/#/");
     var url=environment.api_Url.slice(0,-6);
     
     url=url.concat("/talnet/#/")
      console.log(url);
      this.ngxService.start();
      this.http.post(environment.api_Url+'forgotpassword',{email:this.email,url:url}).subscribe(res => 
      {
         if(res){//if user role is 4(vendor))and unapproved
          
          this.message="Password request email has been sent"
           setTimeout(()=>{  this.router.navigate(['/Login']) }, 4000)
        
        }
      }
        , (err) => {
          this.message = err;
        }
      
      );this.ngxService.stop();
   }

}//end of forgot password
