import { Component, OnInit } from '@angular/core';
import { HttpClient,HttpErrorResponse } from '@angular/common/http';
import { RouterModule,ActivatedRoute ,Router} from '@angular/router';
import { environment } from 'src/environments/environment.prod';
//import { config_logo } from '../../../assets/clientConfig/clientConfig';
import { NotificationService } from 'src/app/Service/notification.service';
import { NgxUiLoaderService } from 'ngx-ui-loader';
import { AuthService} from '../../Service/auth.service';
import {LocalStorageService} from 'ngx-webstorage';
import { AppServiceService } from '../../Service/app-service.service';


import { map } from 'rxjs/operators';


@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  expiry:any;
  logo_url:string;
  messageerror:string;
  config:any={};
  user;
  result:any;
  email:any;
  password:any;
  message:string;
  Userid:number;
  Userrole:number;
  status:any;
  statusText:any;
  

  constructor(private http:HttpClient,private router:Router,private notifyService:NotificationService,private ngxService: NgxUiLoaderService,private auth:AuthService,private localSt:LocalStorageService,
    private appService:AppServiceService)
 { }
  
  ngOnInit(): void {
    this.ngxService.start();
  
   //console.log("the url is "+config_logo);
   this.http.get(environment.api_Url+'getConfig/').subscribe(data => {
    // this.config=(data);
     this.config=data;
     console.log(this.config);
     this.config.Logo=(data[0].Logo);
     this.logo_url=environment.api_Url+this.config.Logo;
  });
    //this.logo_url=config_logo;
     //to display the license expiry information
    this.http.get(environment.api_Url+'validatelicense/').subscribe(res=>{
     
      this.expiry=res;
    },
    err=>
    {
      
      if(err!=null)
      {
      this.messageerror=err.error.messageText;
      this.notifyService.showError(this.messageerror, "TALNET");
     if(this.messageerror=="License Expired")
     {
      this.router.navigate(['/License']);
     } 
    }
    });this.ngxService.stop();
  }

  // function to collect login data and user authentication
  login()
  {
      this.auth.getAuthToken(this.email,this.password).subscribe(
        (res) => {                           //Next callback
          if(res){
            


              //on success stores the user data in local storage from auth service
              this.localSt.store('user',res);

              this.user=this.localSt.retrieve('user');
              this.Userid=this.user[0].userid
              this.Userrole=this.user[0].userrole;
              console.log(this.user[0].userid);
  
              localStorage.setItem('loginStatus','1');
              this.appService.setUserLoggedIn(true);
              this.router.navigate(['/Dashboard']);
          }
        },
        (error) => {                              //Error callback
          console.log(error);
          this.message=error;
          //throw error;   //You can also throw the error to a global error handler
        })
  }

}//end of home component file
