import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Router,ActivatedRoute } from '@angular/router';
import { NgForm } from '@angular/forms';
import { LocalStorageService } from 'ngx-webstorage';
import { environment } from 'src/environments/environment.prod';
//import { config_logo } from '../../../assets/clientConfig/clientConfig';
import { NgxUiLoaderService } from 'ngx-ui-loader';

@Component({
  selector: 'app-reset-password',
  templateUrl: './reset-password.component.html',
  styleUrls: ['./reset-password.component.css']
})
export class ResetPasswordComponent implements OnInit {
  userdetails:any
  newpassword:any;
  confirmpassword:any;
  email:any;
  message:any;  
  message_success:any;
  userid:string;
  token:string;
  validrequest:boolean;
  user;
  httpheaders:string;
  logo_url:string;
  config:any={};
  constructor(private http:HttpClient,private router:Router,private route:ActivatedRoute,private localSt:LocalStorageService,private ngxService: NgxUiLoaderService) { }

  ngOnInit(): void {
	  //this.logo_url=config_logo;
      this.validrequest=false;
      console.log("Resetting Password1");
      this.userid=this.route.snapshot.params['id1'];
      this.token=this.route.snapshot.params['id2'];
     // this.user=this.localSt.retrieve('user');
      //const idToken = this.user[1].accesstoken;
     //const idToken=this.token
     // this.httpheaders="Bearer " + idToken;
     this.http.get(environment.api_Url+'getConfig/').subscribe(data => {
      // this.config=(data);
       this.config=data;
       console.log(this.config);
       this.config.Logo=(data[0].Logo);
       this.logo_url=environment.api_Url+this.config.Logo;
    });
      this.httpheaders="Bearer " + this.token;
      console.log("Resetting Password");
      this.ngxService.start();
      this.http.get(environment.api_Url+'validateforgotpassword/'+this.userid+'/'+this.token/*,{
        headers:{"Authorization":this.httpheaders}
        }*/).subscribe(result => {
        
        this.userdetails=result[0];
        this.email=this.userdetails.Email;
         this.validrequest=true;
      });
      this.ngxService.stop();
}

//function to check user password is same as confirm password 
//and procced to update the new password
  resetpassword(form:NgForm)
  {
    if(this.newpassword===this.confirmpassword)
    {
        this.http.post(environment.api_Url+'resetpassword/'+this.email,{password:this.newpassword},{
        headers:{"Authorization":this.httpheaders}
      }).subscribe(res => 
        {
           if(res){
             this.message_success="Password Reset Successful.Please Login to Proceed."
             setTimeout(()=>{ this.router.navigate(['Home'])}, 4000)
          }
        }
      );
    }else
    {
      this.message="Passwords do not match"
    }
  }
}
//end of reset-password component