 import { Component, OnInit } from '@angular/core';
 import { ActivatedRoute,Router } from '@angular/router';
 import { HttpClient } from '@angular/common/http';
 import { DetailsService } from 'src/app/Service/details.service';
 import { Form, NgForm } from '@angular/forms';
 import { NotificationService } from 'src/app/Service/notification.service';
 import {LocalStorageService} from 'ngx-webstorage';
 import { environment } from 'src/environments/environment.prod';
 import { ToastrService } from 'ngx-toastr';
import * as moment from 'moment';

declare var Razorpay:any;

@Component({
  selector: 'app-licenserenew',
  templateUrl: './licenserenew.component.html',
  styleUrls: ['./licenserenew.component.css'],
  providers:[DetailsService]
})
export class LicenserenewComponent implements OnInit {

  user;
  userdetails:any;
  vendor:any={};
  userid:any;
  userroleno:any;
  role:number;
   httpheaders:string;
  Imagesrc: any;
  adminrole:boolean;
  Details:any={};
  Company_name:any;
  Email:any;
  Mobile:any;
  License_id:any;
  Customer_id:any;
  expirydate:any;
  website:any;
  selectedFile:File;
  imageName:File;
  files:number;
  filesize:number;
  planshow:boolean;
  message:string;
  rzp1;
  paymentdata:any={};
  username:any;
  user_email:any;
  user_mobile:any;
  plan:any;
  users:any;
  Expiry_date:any;
  customerid:any;

  constructor(private http:HttpClient,private route:ActivatedRoute,private router:Router,private service:DetailsService,private notifyService : NotificationService,private localSt:LocalStorageService,private toastr: ToastrService) 
  { }


  ngOnInit(): void {
    this.user=this.localSt.retrieve('user');
    this.userid=this.user[0].userid
    this.userroleno=this.user[0].userrole;
    const idToken = this.user[1].accesstoken;
    this.httpheaders="Bearer " + idToken;
    this.checkuserrole(this.userroleno);
    this.planshow=false;
    
    
  }
  options = {
    "key": "rzp_live_FLxbqahure1OOa", // Enter the Key ID generated from the Dashboard
    "amount": "100", // Amount is in currency subunits. Default currency is INR. Hence, 50000 refers to 50000 paise
    "currency": "INR",
    "name": "Tecple",
    "description": "Talent License Online Payement",
    "image":  "https://tecpleglobal.com/img/tecple-logo.jpg",
   // "order_id": "order_DBJOWzybf0sJbb", //This is a sample Order ID. Pass the `id` obtained in the previous step
   "handler":  (response)=>{
                      console.log(response);
   
                      this.razorPayResponseHandler(response.razorpay_payment_id);
                     this.notifyService.showSuccess("Settings Saved", "TALNET");
   },
    "prefill": {
       /* "name": "Janaki",
        "email": "janakee12@gmail.com",
        "contact": "8220274655"*/
        "name":"",
        "email": "",
        "contact": ""
    },
    "notes": {
        "address": "Razorpay Corporate Office"
    },
    "theme": {
        "color": "#f82249" //screen color
    }
};
  //function to check userrole
  checkuserrole(id:any)
  {
    if(id==1)
    {
      this.adminrole=true;
      this.service.getuserdetailsbyid(this.userid,this.userroleno).subscribe((result)=>{
          this.userdetails=result;
          this.Imagesrc=environment.api_Url+result[0].Image;
          console.warn(result);
          this.username=result[0].FirstName;
          this.user_email=result[0].Email;
          this.user_mobile=result[0].Mobile;
          this.getLicenseData();
          
       });
    }
 }
 getLicenseData()
{
  
  this.http.get(environment.api_Url+'getlicensedata/',{
            headers:{"Authorization":this.httpheaders}
           }).subscribe(data => {
            this.Details=data;
            console.log(this.Details);
          //  this.Details.Company_name=(data[0].CompanyName);
           // this.Details.Email=(data[0].Email);
           // this.Details.Mobile=(data[0].Mobile);
            this.Details.License_id=(data[1].decryptedLicenseid);
            this.Details.Customer_id=(data[0].Customer_id);
            this.Details.License_key=(data[0].License_key);
           this.Details.expirydate=(data[2].expirydate);
           this.Details.users=(data[0].NoUsers);
           this.Expiry_date=this.Details.expirydate;
           this.customerid=this.Details.Customer_id;
                    });
 }
 changeplan()
 {
   this.planshow=true;
 }
 

paylicense(plan:any)
{
  this.plan=plan;
  
  console.log("inside paylicense : " +plan);
  this.options.prefill.name=this.username;
  this.options.prefill.email=this.user_email;
  this.options.prefill.contact=this.user_mobile;
  
  if(plan==1){
    this.options.amount="100";
    this.options.description="Talnet License Renewal for 1 month";
    this.Expiry_date= moment(this.Expiry_date).add(1,'month').format("YYYY-MM-DD");
   this.users=5;
  
  }else if(plan==2){
    this.options.amount="200";
    this.options.description="Talnet License Renewal for 6 months";
    this.Expiry_date= moment(this.Expiry_date).add(6,'month').format("YYYY-MM-DD");
    this.users=5;
  }else if(plan==3)
 {
  this.options.amount="300";
  this.options.description="Talnet License Renewal for 1 year"
  this.Expiry_date= moment(this.Expiry_date).add(12,'month').format("YYYY-MM-DD");
  this.users=10;
 }
 this.rzp1 = new Razorpay(this.options);
 this.rzp1.open();
 this.rzp1.on('payment.failed', function (response){
  alert(response.error.code);
  alert(response.error.description);
  alert(response.error.source);
  alert(response.error.step);
  alert(response.error.reason);
  alert(response.error.metadata.order_id);
  alert(response.error.metadata.payment_id);
});


 }


razorPayResponseHandler(paymentid:any)
{

  // we have come here after payment is done
  //Steps to do now is
  //1. Get the details of payment as we have to insert the details in Payments table
  //2. Set the status of the payment to capture - else Tecple will not get the amount immediately
  //3. Now payment done, we have to update the License table for new Dates

  console.log("paymentid "+paymentid);
  if(paymentid!=null)
  {
    this.http.post(environment.api_Url+'razorpayCapture/',{paymentid:paymentid},{
      headers:{"Authorization":this.httpheaders}
    }).subscribe(result=>{
        if (result) 
        {  // ({ timeout: 10000 });
          setTimeout(() => {
          //inserting into Payment Transaction 
            this.http.post(environment.api_Url+'razorPayTransactionInsert/',{userid: this.userid,paymentid:paymentid},{
              headers:{"Authorization":this.httpheaders}
            }).subscribe(data=>{
              if (data) 
              {
              //api call to update the payment information at Talnet license application 
              this.http.post(environment.api_Url_lic+'TransactionInsert/',{userid: this.userid,paymentid:paymentid},{
                headers:{"Authorization":this.httpheaders}
              }).subscribe(data=>{
                if (data)
                { this.notifyService.showSuccess("Payment Successfull", "TALNET");
                      this.updateLicense();
                } else 
                {
                // this.message = 'Error';
                  this.notifyService.showError("Problem in updating payement information", "TALNET");
                }
                
              }
              );

            } 
            else 
            {
            // this.message = 'Error';
              this.notifyService.showError("Problem in Renewal Payment", "TALNET");
            }
          });},2000)
        }  
        else 
        {
        // this.message = 'Error';
          this.notifyService.showError("Problem in Capturing Payment", "TALNET");
        }
      
     
   });

  }
  

}
updateLicense()
{
    const licenseData = new FormData();
     licenseData.append('customerid', this.Details.Customer_id);
  
      licenseData.append('numlicense', this.users);
      licenseData.append('expiry_date', this.Expiry_date);
  
  //updating License applicaton after expiry dates renewal

  this.http.post(environment.api_Url_lic+'updateLicenseApplication/'+this.Details.Customer_id,{users:this.users,expiry_date:this.Expiry_date},{
    headers:{"Authorization":this.httpheaders}
   }).subscribe(result=>{
    if (result) 
    {

      //this.inserdata=result;
      console.log("License application updated.");
      this.notifyService.showSuccess("License Application Updated ", "TALNET");
      //updating talent database on expiry dates
      this.http.post(environment.api_Url+'updateLicense/'+this.Details.Customer_id,{users:this.users,expiry_date:this.Expiry_date},{
        headers:{"Authorization":this.httpheaders}
      }).subscribe(res=>{
        if (res) 
        {
          this.notifyService.showSuccess("License Purchase Successfull", "TALNET");
          
          this.planshow=false;
          //this.router.navigate(['/LicenseRenewal']);
          this.refresh();
      
        } 
        else 
        {
        // this.message = 'Error';
          this.notifyService.showError("Problem in updating License data", "TALNET");
        }
        
      }
      );

      
    }
    else 
    {
     // this.message = 'Error';
      this.notifyService.showError("Problem in updating License data", "TALNET");
    }
     
   }
   );
  }


  refresh() {
    const prev = this.router.routeReuseStrategy.shouldReuseRoute;
    const prevOSN = this.router.onSameUrlNavigation;
    this.router.routeReuseStrategy.shouldReuseRoute = () => false;
    this.router.onSameUrlNavigation = 'reload';
  
    this.router.navigate(['LicenseRenewal/']);
    setTimeout(() => {
      this.router.routeReuseStrategy.shouldReuseRoute = prev;
      this.router.onSameUrlNavigation = prevOSN;
    }, 0);
  }
}
