import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
//import { config_company_name,config_company_fullname,config_website } from '../../../assets/clientConfig/clientConfig';

import { environment } from 'src/environments/environment.prod';//
@Component({
  selector: 'app-footer',
  templateUrl: './footer.component.html',
  styleUrls: ['./footer.component.css']
})
export class FooterComponent implements OnInit {
  
  company_name:string;
  company_fullname:string;
  config_website:string;
  config_terms:string;
  year:number;
  config:any={};
  constructor(private http:HttpClient) { }

  ngOnInit(): void {
    //this.company_name=config_company_name;   
    //this.company_fullname=config_company_fullname;
    //this.config_website=config_website;
    this.year=(new Date().getFullYear());
    this.http.get(environment.api_Url+'getConfig/').subscribe(data => {
      // this.config=(data);
       this.config=data;
       console.log(this.config);
       this.config.Logo=(data[0].Logo);
      // this.logo_url=environment.api_Url+this.config.Logo;
      this.company_name=(data[0].CompanyName);
      this.company_fullname=(data[0].FullName);
      this.config_website=(data[0].Website);
      this.config_terms=(data[0].TermsPolicy);
    });

  }

}
