 import { Component, OnInit } from '@angular/core';
 import { DetailsService } from 'src/app/Service/details.service';
 import { ActivatedRoute,Router } from '@angular/router';
 import { HttpClient } from '@angular/common/http';
 import { NgForm } from '@angular/forms';
 import { formatCurrency } from '@angular/common';
 import { LocalStorageService } from 'ngx-webstorage';
 import { environment } from 'src/environments/environment.prod';
 import * as XLSX from 'xlsx';
 import { NgxUiLoaderService } from 'ngx-ui-loader';

 @Component({
   selector: 'app-closed',
   templateUrl: './closed.component.html',
   styleUrls: ['./closed.component.css'],
   providers:[DetailsService],
 })
 export class ClosedComponent implements OnInit {
   user;
   CloseJDForm:NgForm;
   userid:any;
   userroleno:any;
   userdetails:any;
   userrole:boolean;
   startdate:any;
   enddate:any;
   details:any;
   message:any;
   nodata:any;
   status:boolean;
   prevdate:any;
   date: Date = new Date();
   title30:boolean;
   title:boolean;
   httpheaders:string;0
   paginateflag:Boolean;
   fileName= 'Report_JD_Closed.xlsx';
   
   constructor(private http:HttpClient,private route:ActivatedRoute,private router:Router,private service:DetailsService,private localSt:LocalStorageService,private ngxService: NgxUiLoaderService){
         
  }
   ngOnInit(): void {
     //fetching user details 
	 console.log("Inside CLosed Vendor");
     this.user=this.localSt.retrieve('user');
     this.userid=this.user[0].userid
     this.userroleno=this.user[0].userrole;
     const idToken = this.user[1].accesstoken;
     this.httpheaders="Bearer " + idToken;
     this.checkuserrole(this.userroleno);
     //setting the current date and - 30 days for inital report
     this.prevdate=new  Date(Date.now() - 30 * 24 * 60 * 60 * 1000)
     this.startdate= this.prevdate.toISOString().substr(0, 10);
     this.enddate=this.date.toISOString().substr(0, 10);
     this.title30=true;
     this.title=false;
      /***Init page loader */
     const initload = new FormData();
 
     initload.append('startdate',this.startdate);
     initload.append('enddate', this.enddate);
     this.ngxService.start();
     if(this.userroleno==2||this.userroleno==1)
       {
           this.http.post(environment.api_Url+'closedJD',initload,{
             headers:{"Authorization":this.httpheaders}
            }).subscribe(data => {
             
             this.details=data;
             if(this.details.length==0)
             {
               this.status=true;
             this.nodata="No Data available betweeen this dates";
             }
             else{
				if (Object.keys(data).length > 10)
					 {this.paginateflag=true;}
				else { this.paginateflag=false;}
                  
               this.status=false;
               this.message=true;
             } 
         });
       }
       else if(this.userroleno==3)//for HR show only JD reports that he created
       {
           console.log("Inside HR");
           this.http.post(environment.api_Url+'closedJDforHr/'+this.userid,initload,{
             headers:{"Authorization":this.httpheaders}
             }).subscribe(data => {
              
               this.details=data;
               console.warn("hr details",this.details);
               if(this.details.length==0)
               {
                 this.status=true;
                 this.nodata="No Data available betweeen this dates";
               }
               else{
				 if (Object.keys(data).length > 10)
					 {this.paginateflag=true;}
				else { this.paginateflag=false;}
                  
                 this.status=false;
                 this.message=true;
               }       
         });
 
       }else if(this.userroleno==4){
         
           this.http.post(environment.api_Url+'closedJDVendor/'+this.userid,initload,{
             headers:{"Authorization":this.httpheaders}
            }).subscribe(data => {
              
                 this.details=data;
                 if(this.details.length==0)
                 {
                   this.status=true;
                   this.nodata="No Data available betweeen this dates";
                 }
                 else{
					 if (Object.keys(data).length > 10)
						 {this.paginateflag=true;}
					else { this.paginateflag=false;}
                   
                   this.status=false;
                   this.message=true;
 
                   }
               });
       }   this.ngxService.stop();
  }
  
 //check the user role for HR
    checkuserrole(id:any)
    {
      if(id==3){
       this.userrole=true;
      }
      else{
       this.userrole=false;
      }
    }

 //function to navigate to add JD page
   AddJD(){
      this.router.navigate(['/Add']);
   }
   //function to navigate to Dashboard Page
   Dashboard(){
     this.router.navigate(['/Dashboard'])
   }
   
   //function to navigate to Published Jd Page
   publish(){
     this.router.navigate(['/PublishedJD']);
   }

   //function to navigate to resume upload page
   resumeupload(id:any){   /*JDID**userid**userroleid*/
     this.router.navigate(['/ResumeUpload',id]);
     console.warn(this.userroleno);
   }

  //function to fetch the report of selected dates
   collectData(form:NgForm){
     this.title30=false;
     this.title=true;
     console.log(this.startdate);
     this.ngxService.start();
     if(this.userroleno==2||this.userroleno==1)
             {
                 this.http.post(environment.api_Url+'closedJD',form.value,{
                   headers:{"Authorization":this.httpheaders}
                  }).subscribe(data => {
                   
                     this.details=data;
                     if(this.details.length==0)
                     {
                       this.status=true;
                       this.nodata="No Data available betweeen this dates";
                     }
                     else{
						 if (Object.keys(data).length > 10)
							  {this.paginateflag=true;}
						 else { this.paginateflag=false;}
                  
                       this.status=false;
                       this.message=true;
                     } 
                 });
             }
     else if(this.userroleno==4)
	 {
		  this.http.post(environment.api_Url+'closedJDVendor/'+this.userid,form.value,{
                   headers:{"Authorization":this.httpheaders}
                  }).subscribe(data => {
                   
                     this.details=data;
                     if(this.details.length==0)
                     {
                       this.status=true;
                       this.nodata="No Data available betweeen this dates";
                     }
                     else{
						 if (Object.keys(data).length > 10)
							  {this.paginateflag=true;}
						 else { this.paginateflag=false;}
                  
                       this.status=false;
                       this.message=true;
                     } 
                 });
	 }
	 else 
    {
     this.http.post(environment.api_Url+'closedJDforHr/'+this.userid,form.value,{
       headers:{"Authorization":this.httpheaders}
      }).subscribe(data => {
         
           this.details=data;
           console.warn("hr details",this.details);
           if(this.details.length==0)
           {
			   
             this.status=true;
             this.nodata="No Data available betweeen this dates";
           }
           else{
			   if (Object.keys(data).length > 10)
				    {this.paginateflag=true;}
			   else { this.paginateflag=false;}
                  
             this.status=false;
             this.message=true;
           }       
       });
      }   this.ngxService.stop();
   }
 //function to download the report as excel  
 exportexcel()
{
 /* pass here the table id */
 let element = document.getElementById('report-table');
 const ws: XLSX.WorkSheet =XLSX.utils.table_to_sheet(element);

 /* generate workbook and add the worksheet */
 const wb: XLSX.WorkBook = XLSX.utils.book_new();
 XLSX.utils.book_append_sheet(wb, ws, "Sheet1");

 /* save to file */  
 XLSX.writeFile(wb, this.fileName);
}

 }//end of closed component file
 