import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, UrlTree } from '@angular/router';
import { Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { catchError, map} from 'rxjs/operators';
import { environment } from 'src/environments/environment.prod';

@Injectable({
  providedIn: 'root'
})
export class LicGuard implements CanActivate {

  constructor(private http:HttpClient,private router:Router){

  }
  canActivate(next: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<boolean>
    { //this.http.get('/api/validatelicense/')
      return this.http.get(environment.api_Url+'validatelicense') 
      .pipe(
      map(res => {
        if (res['Error']) {
          alert("License Expired!");
          this.router.navigate(['/License']);
          return false;
        } else {
          return true;
        }
      }),
      catchError((error) => {
        this.router.navigate(['/License']);
        return of(false);
      })
    );

      /*if (this.http.get('/api/validatelicense/'))
       {
         console.log("License valid")
        return true;
      }
      else
      {
        console.log("License not valid")
        this.router.navigate(['/License']);
        return false;
      } */
   
  }
 
}
