import { Component, OnInit } from '@angular/core';
//import { config_logo } from '../../../assets/clientConfig/clientConfig';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment.prod';

@Component({
  selector: 'app-license',
  templateUrl: './license.component.html',
  styleUrls: ['./license.component.css']
})
export class LicenseComponent implements OnInit {
  logo_url:string;
  config:any={};
  constructor(private http:HttpClient) { }

  ngOnInit(): void {
   // this.logo_url=config_logo;
   this.http.get(environment.api_Url+'getConfig/').subscribe(data => {
    // this.config=(data);
     this.config=data;
     console.log(this.config);
     this.config.Logo=(data[0].Logo);
     this.logo_url=environment.api_Url+this.config.Logo;
  });
  }

}
