import { Component, OnInit } from '@angular/core';
import { ActivatedRoute,Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { DetailsService } from 'src/app/Service/details.service';
import { NgForm } from '@angular/forms';
import { NotificationService } from 'src/app/Service/notification.service';
import {LocalStorageService} from 'ngx-webstorage';
import { environment } from 'src/environments/environment.prod';
import { element } from 'protractor';
import { ElementRef,ViewChild } from '@angular/core';
const FileSaver = require('file-saver');
@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css'],
  providers:[DetailsService]
})
export class ProfileComponent implements OnInit {
  @ViewChild('input') vc: ElementRef;
  user;
  userdetails:any;
  vendor:any={};
  userid:any;
  userroleno:any;
  role:number;
  selectedFile:File;
  imageName:File;
  files:number;
  filesize:number;
  message:string;
  alert:any;
  firstname:any;
  lastname:any;
  contactname:any;
  phone:any;
  email:any;
  contract:any;
  oldpwd:any;
  newpwd:any;
  cnewpwd:any;
  vendorrole:boolean;
  adminrole:boolean;
  editstat:boolean;
  flag:boolean;
  httpheaders:string;
  Imagesrc: any;
  constructor(private http:HttpClient,private route:ActivatedRoute,private router:Router,private service:DetailsService,private notifyService : NotificationService,private localSt:LocalStorageService) 
  { }

  ngOnInit(): void {
   //fetch logged in user data
    this.user=this.localSt.retrieve('user');
    this.userid=this.user[0].userid
    this.userroleno=this.user[0].userrole;
    const idToken = this.user[1].accesstoken;
    this.httpheaders="Bearer " + idToken;
    this.checkuserrole(this.userroleno);
    this.editstat=false;
    this.flag=true;
  
  }
 //function to check userrole
  checkuserrole(id:any)
  {
    if(id==4)
    {
      this.vendorrole=true;
      this.adminrole=false;
      console.log("Inside usr vendor");
      this.service.getuserdetailsbyid(this.userid,this.userroleno).subscribe((result)=>{
       this.http.get(environment.api_Url+'vendordetails/'+this.userid,{
        headers:{"Authorization":this.httpheaders}
       }).subscribe(data=>{
            this.userdetails=data;
            console.log(data);
          this.userdetails.VendorCompanyName=data[0].VendorCompanyName;
          this.userdetails.ContactPersonName=data[0].ContactPersonName;
        
          this.userdetails.phone=data[0].Mobile;
          this.userdetails.email=data[0].Email;
            this.Imagesrc=environment.api_Url+data[0].Image;
            console.warn(data);
            this.userdetails.contract=data[0].AgreementDoc;
     });
    });
    }
    else
    {
      this.vendorrole=false;
      this.adminrole=true;
      console.log("Inside usr admin");
      this.service.getuserdetailsbyid(this.userid,this.userroleno).subscribe((result)=>{
          this.userdetails=result;
          console.log(result);
          this.userdetails.firstname=result[0].FirstName;
          this.userdetails.lastname=result[0].LastName;
          
          this.userdetails.phone=result[0].Mobile;
          this.userdetails.email=result[0].Email;
          this.Imagesrc=environment.api_Url+result[0].Image;
          console.warn(result);
       });
    }
 }

//function to select the photo file 
  public onFileChanged(event){
    this.selectedFile = event.target.files;
    console.warn(this.selectedFile[0].name);
    this.filesize=this.selectedFile[0].size;
     console.warn(this.filesize);
 }

  updatephoto(form:NgForm)
  {
   if(this.selectedFile==null)
   {
    this.notifyService.showError("No file Selected", "TALNET");
   }else{
    if(this.filesize<=50000)
    {
      const uploadImageData = new FormData();
      uploadImageData.append('userid',this.userid);
      uploadImageData.append('userrole',this.userroleno);
      uploadImageData.append('imageName',this.selectedFile[0],this.selectedFile[0].name);
      console.log("Iam here")
      console.log(uploadImageData);
      this.http.post(environment.api_Url+'updatephoto',uploadImageData,{
        headers:{"Authorization":this.httpheaders}
      }).subscribe(res => 
       {
          this.notifyService.showSuccess("Your Picture has updated successfully", "TALNET");
          //window.location.reload();
		  this.refresh();
         }
       );
    }
    else{
      this.notifyService.showError("Image size should be less than 50KB", "TALNET");
      }
   }
  }
 
 //function to update phone number
  updateinfo(form:NgForm)
  {
    const updateinfo = new FormData();
    updateinfo.append('userid',this.userid);
    updateinfo.append('userrole',this.userroleno);
    updateinfo.append('phone', this.userdetails.phone);
    this.http.post(environment.api_Url+'updateuserinfo',updateinfo,{
      headers:{"Authorization":this.httpheaders}
     }).subscribe(res => 
      { 
        this.flag=true;
        this.notifyService.showSuccess("Your Profile is updated successfully", "TALNET");
       // window.location.reload();
	   this.refresh();
        
      });
}

//function to update the password
updatepassword(form:NgForm)
{ 
  const updatepwd = new FormData();
  if(this.newpwd==this.cnewpwd)
  {
    updatepwd.append('userid',this.userid);
    updatepwd.append('oldpwd',this.oldpwd);
    updatepwd.append('newpwd',this.newpwd);
    updatepwd.append('cnewpwd',this.cnewpwd);
    this.http.post(environment.api_Url+'updatepwd',updatepwd,{
      headers:{"Authorization":this.httpheaders}
    }).subscribe(res => 
      {
        this.notifyService.showSuccess("Your Password is updated successfully", "TALNET")
       // window.location.reload();
	   this.refresh();
      },(err)=>{
        this.message=err;
        this.notifyService.showError("Please check your Old password", "TALNET")
      });
  }
  else{
    this.notifyService.showError("New Password and confirm Password do not match", "TALNET")
    }
}

//function to allow to edit phone
editphone()
{
  console.log("clicked now");
  this.flag=false;
  this.vc.nativeElement.focus();
  
}


//function to refresh the component
refresh() {
  const prev = this.router.routeReuseStrategy.shouldReuseRoute;
  const prevOSN = this.router.onSameUrlNavigation;
  this.router.routeReuseStrategy.shouldReuseRoute = () => false;
  this.router.onSameUrlNavigation = 'reload';

  this.router.navigate(['EditProfile']);
  setTimeout(() => {
    this.router.routeReuseStrategy.shouldReuseRoute = prev;
    this.router.onSameUrlNavigation = prevOSN;
  }, 0);
}


downloadPdf(pdfUrl: string, pdfName: string) {
  pdfUrl=environment.api_Url+pdfUrl;
  FileSaver.saveAs(pdfUrl, pdfName);
}
}//end of profile component file
