import { Injectable } from '@angular/core';
import {HttpClient } from '@angular/common/http';
import {NgForm} from '@angular/forms';
import { LocalStorageService } from 'ngx-webstorage';
import { environment } from 'src/environments/environment.prod';

@Injectable({
  providedIn: 'root'
})
export class DetailsService {
 url:any;
 user:any;
 httpheaders:string;
  constructor(private http:HttpClient,private localSt:LocalStorageService) { 
    this.user=this.localSt.retrieve('user');
  
  const idToken = this.user[1].accesstoken;
  this.httpheaders="Bearer " + idToken;
  }
  
  getuserdetailsbyid(userid:any,userroleno:any)
  {
    console.warn(userid);
    console.warn(userroleno);
    if(userroleno==3||userroleno==1||userroleno==2)      /**for hr,admin,manager(name) taken from usermaster */
    {
      this.url=environment.api_Url+"hrdetails"
      return this.http.get(`${this.url}/${userid}`,
      {
                  headers:{"Authorization":this.httpheaders}
                 }) 

    }
    else if(userroleno==4)
    {
      this.url=environment.api_Url+"vendordetails"    /** for vendor(name) taken from vendormaster */
      return this.http.get(`${this.url}/${userid}`,
      {
                  headers:{"Authorization":this.httpheaders}
                 })
    }
  }
  getdashboardmetricsbyid(userid:any,userroleno:any){
    if(userroleno==3){                                  /**for hr */
      this.url=environment.api_Url+"Dashboardforhr"
      return this.http.get(`${this.url}/${userid}`,{
        headers:{"Authorization":this.httpheaders}
       })
    }
    else if(userroleno==4)                              /**for vendor */
    {
       this.url=environment.api_Url+"Dashboardforvendor"
       return this.http.get(`${this.url}/${userid}`,{
        headers:{"Authorization":this.httpheaders}
       })
    }
    else if(userroleno==1||userroleno==2)            /**for admin and manager */
    {
      this.url=environment.api_Url+"Dashboardforadmin"
      return this.http.get(`${this.url}/${userid}`,{
        headers:{"Authorization":this.httpheaders}
       })
    }
    
  }
  
}
