 import { Component, OnInit } from '@angular/core';
 import { DetailsService } from 'src/app/Service/details.service';
 import { ActivatedRoute,Router } from '@angular/router';
 import { HttpClient } from '@angular/common/http';
 import { NgForm } from '@angular/forms'; 
 import { LocalStorageService } from 'ngx-webstorage';
 import { environment } from 'src/environments/environment.prod';
 import { NgxUiLoaderService } from 'ngx-ui-loader';
 
 //declare var require: any;
 const FileSaver = require('file-saver');
 @Component({
   selector: 'app-search',
   templateUrl: './search.component.html',
   styleUrls: ['./search.component.css'],
   providers:[DetailsService]
 })
 export class SearchComponent implements OnInit {
   showdata:boolean;
   searchby:any;
   title:boolean;
   errmessage:boolean;
   searchdata:any;
   user;
   userid:any;
   userroleno:any;
   userrole:boolean;
   httpheaders:any;
   cp: number = 1;
   paginateflag:Boolean;
   constructor(private http:HttpClient,private route:ActivatedRoute,private router:Router,private service:DetailsService,private localSt:LocalStorageService,private ngxService: NgxUiLoaderService) { }
 
   ngOnInit(): void {
     this.showdata=false;
     
     this.user=this.localSt.retrieve('user');
      this.userid=this.user[0].userid
      this.userroleno=this.user[0].userrole;
      const idToken = this.user[1].accesstoken;
      this.httpheaders="Bearer " + idToken;
      this.checkuserrole(this.userroleno);
   }
   search(form:NgForm)
   {
     this.errmessage=false;
     this.ngxService.start();
     console.log("display search values");
     //getSearchResult
      //fetching search  resumes 
      if(this.searchby=="")
      {
        this.errmessage=true;
        this.showdata=false;
        this.title=false;
      }
      else
      {
      if(this.userroleno==3)
        {
         this.http.post(environment.api_Url+'getSearchResult/'+this.userid,form.value,{
           headers:{"Authorization":this.httpheaders}
          }).subscribe(data => {
               this.searchdata=data;
            //   console.warn("hr details",this.searchdata);
               if(this.searchdata.length==0)
               {
                 this.errmessage=true;
                 this.showdata=false;
                 this.title=false;
              
               }
               else{
                 this.showdata=true;
                 this.title=true;
                 this.errmessage=false;
                 if (Object.keys(data).length > 10)
                 {this.paginateflag=true;}
                else { this.paginateflag=false;}
                         
               }       
           });
         
        }
      else{
         this.http.post(environment.api_Url+'getSearchResult/'+0,form.value,{
       headers:{"Authorization":this.httpheaders}
      }).subscribe(data => {
           this.searchdata=data;
           console.log(this.searchdata);
        //   console.warn("hr details",this.searchdata);
           if(this.searchdata.length==0)
           {
             this.errmessage=true;
             this.showdata=false;
             this.title=false;
          
           }
           else{
             this.showdata=true;
             this.title=true;
             this.errmessage=false;
             if (Object.keys(data).length > 10)
             {this.paginateflag=true;}
            else { this.paginateflag=false;}
                           
           }       
       });
     }
    }
    this.ngxService.stop();
 
   }
   //check the user role for HR
   checkuserrole(id:any)
   {
     if(id==4){
       this.showdata=false;
     }
    
   }
   //function to download the resume attachment
   downloadPdf(pdfUrl: string, pdfName: string) {
     pdfUrl=environment.api_Url+pdfUrl;
     FileSaver.saveAs(pdfUrl, pdfName);
   }
 
 }
 