 import { Component, OnInit } from '@angular/core';
 import { ActivatedRoute,Router } from '@angular/router';
 import { HttpClient } from '@angular/common/http';
 import { DetailsService } from 'src/app/Service/details.service';
 import { Form, NgForm } from '@angular/forms';
 import { NotificationService } from 'src/app/Service/notification.service';
 import {LocalStorageService} from 'ngx-webstorage';
 import { environment } from 'src/environments/environment.prod';

@Component({
  selector: 'app-settings',
  templateUrl: './settings.component.html',
  styleUrls: ['./settings.component.css']
})
export class SettingsComponent implements OnInit {

  user;
  userdetails:any;
  vendor:any={};
  userid:any;
  userroleno:any;
  role:number;
  editstat:boolean;
  readableflag:boolean;
  httpheaders:string;
  Imagesrc: any;
  adminrole:boolean;
  Edit:any={};
  compName:any;
  fullname:any;
  logo:any;
  website:any;
  selectedFile:File;
  imageName:File;
  files:number;
  filesize:number;
  uploadlogo:boolean;
  message:string;
  constructor(private http:HttpClient,private route:ActivatedRoute,private router:Router,private service:DetailsService,private notifyService : NotificationService,private localSt:LocalStorageService) 
  { }


  ngOnInit(): void {
    this.user=this.localSt.retrieve('user');
    this.userid=this.user[0].userid
    this.userroleno=this.user[0].userrole;
    const idToken = this.user[1].accesstoken;
    this.httpheaders="Bearer " + idToken;
    this.checkuserrole(this.userroleno);
    this.editstat=false;
    this.readableflag=true;
    this.uploadlogo=false;
    this.getConfig();
    
  }
  //function to check userrole
  checkuserrole(id:any)
  {
    if(id==1)
    {
      this.adminrole=true;
      this.service.getuserdetailsbyid(this.userid,this.userroleno).subscribe((result)=>{
          this.userdetails=result;
          this.Imagesrc=environment.api_Url+result[0].Image;
          console.warn(result);
          
       });
    }
 }
 getConfig()
{
  
  this.http.get(environment.api_Url+'getConfig/',{
            headers:{"Authorization":this.httpheaders}
           }).subscribe(data => {
            this.Edit=data;
            console.log(this.Edit);
            this.Edit.compName=(data[0].CompanyName);
            this.Edit.fullname=(data[0].FullName);
            this.Edit.website=(data[0].Website);
            this.Edit.logo=(data[0].Logo);
            this.Imagesrc=environment.api_Url+data[0].Logo;

           });
 }
 updateconfig(form:NgForm)
 {

  if(this.uploadlogo==true)
  {
    if (this.filesize <= 200000)
    {
      const uploadData = new FormData();
     uploadData.append('compName', this.Edit.compName);
     uploadData.append('fullname', this.Edit.fullname);
     uploadData.append('website', this.Edit.website);
     uploadData.append('logo', this.selectedFile[0], this.selectedFile[0].name);
     this.http.post(environment.api_Url+'updateconfiglogo',uploadData,{
      headers:{"Authorization":this.httpheaders}
    }).subscribe(response=> {
      if (response) 
      {
        this.message = 'Saved Settings';
        this.notifyService.showSuccess("Settings Saved", "TALNET");
        this.router.navigate(['/Dashboard']);
      } 
      else 
      {
        this.message = 'Error';
        this.notifyService.showError("Problem in updating the Settings", "TALNET");
      } 

    }
    );
    }else {
      this.notifyService.showError("File should be less than 200KB.", "TALNET");
      }

  } else{
      this.http.post(environment.api_Url+'updateconfig',form.value,{
      headers:{"Authorization":this.httpheaders}
     }).subscribe(data=>{
      if (data) 
      {
        this.message = 'Settings Saved';
        this.notifyService.showSuccess("Settings Saved", "TALNET");
        this.router.navigate(['/Dashboard']);
      } 
      else 
      {
        this.message = 'Error';
        this.notifyService.showError("Problem in updating the Settings", "TALNET");
      } 
    });
  } 
 }
 
//function to select the photo file 
public onFileChanged(event){
  this.selectedFile = event.target.files;
  console.warn(this.selectedFile[0].name);
  this.filesize=this.selectedFile[0].size;
   console.warn(this.filesize);
   this.uploadlogo=true;
}

public editConfig()
{
  console.log("clicked now");
  this.readableflag=false;
  
}
public cancelConfig()
{
 
  this.readableflag=true;
  
}

}
