declare var require: any;
const FileSaver = require('file-saver');
@Component({
  selector: 'app-user-details',
  templateUrl: './user-details.component.html',
  styleUrls: ['./user-details.component.css'],
  providers:[DetailsService]
})
export class UserDetailsComponent implements OnInit {
  
  @ViewChild('termsoption') content: any;
  @ViewChild('termsmodalclose') termsmodalclose;
user;
userdetails:any;
userid:number;
userroleno:number;
Details:any;
role:number;
hrrole:boolean;
vendorrole:boolean;
adminrole:boolean;
vendelete:boolean;
httpheaders:string;
Imagesrc:any;
message:string;
show:boolean;
options:any;
setTerms:any;
VendorID:any;
closeResult: String;
comments:any;
vid:any;
avg:any;

  constructor(private modalService: NgbModal,private http:HttpClient,private route:ActivatedRoute,private router:Router,private service:DetailsService,private notifyService : NotificationService,private localSt:LocalStorageService) { }

  ngOnInit() {
    
    this.options = [{ id: 1, name: "T & C to appear for every Jd" }, { id: 2, name: "T & C to appear only once " }];
    //retrieving user data from local storage
    this.user=this.localSt.retrieve('user');
    this.userid=this.user[0].userid
    this.userroleno=this.user[0].userrole;
    const idToken = this.user[1].accesstoken;
    this.httpheaders="Bearer " + idToken;
    this.role=this.route.snapshot.params['id1'];
   this.getid(this.role);
   //fetching user details based on id
    this.service.getuserdetailsbyid(this.userid,this.userroleno).subscribe((result)=>{
      this.userdetails=result;
      console.warn(result);
    }); 
  }
  /**function to  fetch vendor and HR details based on userid**/
  getid(id:any)
  {
    if(id==1)
    {
      this.hrrole=true;
      this.http.get(environment.api_Url+'viewhr',{
        headers:{"Authorization":this.httpheaders}
       }).subscribe(data=>{
            this.Details=(data[0]);
            console.log(this.Details)

            for(var i=0;i<Object.keys(data[0]).length;i++)
            {
              this.Details[i].Image=environment.api_Url+this.Details[i].Image;
              console.log(this.Details[i].Image);

            }
            if(Object.keys(data).length ==0)
            {
              this.message="You have no data to display"
              this.show=true;
            }
            console.warn(this.Details);
          });
    }
    else if(id==2)
    {
      this.vendorrole=true;
      this.vendelete=true;
      if(this.userroleno==3)
      {
        this.http.get(environment.api_Url+'viewvendorbyHR/'+this.userid,{
          headers:{"Authorization":this.httpheaders}
        }).subscribe(data=>{
        
          this.Details=(data[0]);
          console.log( this.Details);
          
          for(var i=0;i<Object.keys(data[0]).length;i++)
          {
            this.Details[i].Image=environment.api_Url+this.Details[i].Image;
            console.log( this.Details[i].Image);
          }
          console.warn(this.Details);
          this.vendelete=false;
          if(Object.keys(data).length ==0)
          {
            this.message="You have no data to display"
            this.show=true;
          }
        });
      }
      else
      {
        this.http.get(environment.api_Url+'viewvendor',{
          headers:{"Authorization":this.httpheaders}
        }).subscribe(data=>{
          this.Details=(data[0]);
          //const URL= environment.api_Url;
          //this.Details.Image=environment.api_Url+this.Details.Image;
          console.log( this.Details);
          for(var i=0;i<Object.keys(data[0]).length;i++)
          {
            this.Details[i].Image=environment.api_Url+this.Details[i].Image;
            console.log( this.Details[i].Image);
          }
          
          if(Object.keys(data).length ==0)
          {
            this.message="You have no data to display"
            this.show=true;
          }
        });
      }
      
    } 
  }


/**function download the contract document */
  downloadPdf(pdfUrl: string,pdfName: string){
	pdfUrl=environment.api_Url+pdfUrl;
    FileSaver.saveAs(pdfUrl, pdfName);
  }

/**function to delete the vendor/HR **/
deleteUser(uid:any,vid:any)
{
  var conf = confirm("Are you sure you want to delete?");
  if (conf==true) 
  {
    if(this.vendorrole)
    {
      this.http.post(environment.api_Url+'delete_vendor/'+uid,{vid:vid,userid:this.userid},{
        headers:{"Authorization":this.httpheaders}
        }).subscribe(data=>{
            this.Details=(data);
            console.warn(this.Details);
           // window.location.reload();
           this.refresh();
            this.notifyService.showSuccess("The Vendor is deleted successfully", "TALNET");
        });
     }else if(this.hrrole)
      {
        this.http.post(environment.api_Url+'delete_hr/',{uid:uid,userid:this.userid},{
          headers:{"Authorization":this.httpheaders}
          }).subscribe(data=>{
            this.Details=(data);

            console.warn(this.Details.length);
            //window.location.reload();
            this.refresh();
            this.notifyService.showSuccess("The HR is deleted successfully", "TALNET");
         });
      }
  }else {
      return false;
     }

  }
  refresh() {
    const prev = this.router.routeReuseStrategy.shouldReuseRoute;
    const prevOSN = this.router.onSameUrlNavigation;
    this.router.routeReuseStrategy.shouldReuseRoute = () => false;
    this.router.onSameUrlNavigation = 'reload';
    if(this.hrrole){
      this.router.navigate(['UserDetails/',1]);

    }else if(this.vendorrole)
    {
    this.router.navigate(['UserDetails/',2]);
    }
    setTimeout(() => {
      this.router.routeReuseStrategy.shouldReuseRoute = prev;
      this.router.onSameUrlNavigation = prevOSN;
    }, 0);
  }
 

/**function to delete the vendor/HR **/
saveTcoption(terms:any)
{
 /* const Termsdata = new FormData();
  Termsdata.append('vid', this.VendorID );
  Termsdata.append('setTerms', this.setTerms);*/

  this.http.post(environment.api_Url+'saveTcoption/',{vid:this.vid,terms:terms},{
    
    headers:{"Authorization":this.httpheaders}
    }).subscribe(data=>{
        this.Details=(data);
        console.warn(this.Details);
       // window.location.reload();
       this.refresh();
       this.termsmodalclose.nativeElement.click();
        this.notifyService.showSuccess("The T&C options changed for the Vendor", "TALNET");
    });
}





opensetterms(vid:any,setterms:any)
{
 this.vid=vid;

  this.setTerms=setterms;
  this.content.open();
}
}




