import { Component, ViewChild } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { config_company_name } from '../app/clientConfig/clientConfig.js';
import { ActivatedRoute,Router } from '@angular/router';
import { Idle, DEFAULT_INTERRUPTSOURCES } from '@ng-idle/core';
import { Keepalive } from '@ng-idle/keepalive';
import { NgbModal, NgbModalRef }  from '@ng-bootstrap/ng-bootstrap';
import { AppServiceService } from '../app/Service/app-service.service';
import {LocalStorageService} from 'ngx-webstorage';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'ATS';
  idleState = 'Not started.';
  timedOut = false;
  lastPing?: Date = null;
  title1 = 'angular-idle-timeout';
 // public modalRef: NgbModalRef;
   button_stay= true;
   
  @ViewChild('childModal', { static: false }) childModal: any;
   
  constructor(private idle: Idle, private keepalive: Keepalive,private router:Router,private modalService: NgbModal, private appService:AppServiceService,private titleService: Title,private localSt:LocalStorageService) {
    this.setTitle(config_company_name);
    // sets an idle timeout of 5 seconds, for testing purposes.
    idle.setIdle(900);//15 mins
   /// idle.setIdle(60);
    // sets a timeout period of 5 seconds. after 10 seconds of inactivity, the user will be considered timed out.
    idle.setTimeout(100);//5mins
   //idle.setTimeout(30);//5mins
    // sets the default interrupts, in this case, things like clicks, scrolls, touches to the document
    idle.setInterrupts(DEFAULT_INTERRUPTSOURCES);

    idle.onIdleEnd.subscribe(() => {
      this.idleState = 'No longer idle.'
      console.log(this.idleState);
      this.reset();
    });

    idle.onTimeout.subscribe(() => {
      this.idleState = 'Timed out!';
      this.timedOut = true;
      console.log(this.idleState);
      this.button_stay=false;
      //this.router.navigate(['/']);
     // this.router.navigate(['Logout']);
     this.logout();
    });

    idle.onIdleStart.subscribe(() => {
        this.idleState = 'You\'ve gone idle!'
       // console.log(this.idleState);
        this.childModal.show();
    });

    idle.onTimeoutWarning.subscribe((countdown) => {
      this.idleState = 'You will time out in ' + countdown + ' seconds!'
     // console.log(this.idleState);
    });

    // sets the ping interval to 15 seconds
    keepalive.interval(15);

    keepalive.onPing.subscribe(() => this.lastPing = new Date());

   // this.reset();
   this.appService.getUserLoggedIn().subscribe(userLoggedIn => {
    if (userLoggedIn) {
      idle.watch()
      this.timedOut = false;
    } else {
      idle.stop();
    }
  })
  }

  reset() {
    this.idle.watch();
    this.idleState = 'Started.';
    this.timedOut = false;
  }
  hideChildModal(): void {
  this.childModal.hide();
}

stay() {
  this.childModal.hide();
  this.reset();
}

logout() {
  this.childModal.hide();
  this.localSt.clear('user');
  this.appService.setUserLoggedIn(false);
  this.router.navigate(['Logout']);
}

  public setTitle(newTitle: string) {
    this.titleService.setTitle("WELCOME TO "+newTitle);
  }
}
