

import { CommonModule } from '@angular/common';
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AppRoutingModule } from './app-routing.module';
import { MatButtonModule} from '@angular/material/button';
import { MatMenuModule} from '@angular/material/menu';
import { AppComponent } from './app.component';
import { FormsModule,ReactiveFormsModule } from '@angular/forms';
import { DetailsService } from './Service/details.service';
import { CKEditorModule } from '@ckeditor/ckeditor5-angular';
import Image from '@ckeditor/ckeditor5-image/src/image';
import ImageToolbar from '@ckeditor/ckeditor5-image/src/imagetoolbar';
import ImageStyle from '@ckeditor/ckeditor5-image/src/imagestyle';
import ImageResize from '@ckeditor/ckeditor5-image/src/imageresize';
import LinkImage from '@ckeditor/ckeditor5-link/src/linkimage';
import { ToastrModule } from 'ngx-toastr';
import { NgxWebstorageModule } from 'ngx-webstorage';
import { HttpClientModule,HTTP_INTERCEPTORS } from '@angular/common/http';
import { HttperrorInterceptor} from './httperror.interceptor';
import { NgMultiSelectDropDownModule } from 'ng-multiselect-dropdown';
import { NgIdleKeepaliveModule } from '@ng-idle/keepalive'; // this includes the core NgIdleModule but includes keepalive providers for easy wireup
import { MomentModule } from 'ngx-moment';
import { ModalModule } from 'ngx-bootstrap/modal';
import { NgxPaginationModule } from 'ngx-pagination';//to include pagination in table
import { NgxUiLoaderModule } from 'ngx-ui-loader'; //to include page loader spinner

/**shared */
import { HomeComponent } from './shared/home/home.component';
import { LoginComponent } from './shared/login/login.component';
import { RegistrationComponent } from './shared/registration/registration.component';
import { DashboardComponent } from './shared/dashboard/dashboard.component';
import { PublishedComponent } from './shared/published/published.component';
import { ClosedComponent } from  './shared/closed/closed.component';
import { HeaderComponent } from './shared/header/header.component';
import { FooterComponent } from './shared/footer/footer.component';
import { FileuploadComponent } from './shared/fileupload/fileupload.component';
import { ProfileComponent } from './shared/profile/profile.component';
import { LicenseComponent } from './shared/license/license.component';
import { ForgotpasswordComponent } from './shared/forgot-password/forgot-password.component';
import { ResetPasswordComponent } from './shared/reset-password/reset-password.component';
import { PrivacypolicyComponent } from './shared/privacypolicy/privacypolicy.component';
import { MetricsComponent } from './shared/metrics/metrics.component';
import { SearchComponent } from './shared/search/search.component'; 
import { CandidatereportComponent } from  './shared/candidatereport/candidatereport.component';

/**Hr  */
import {AddJDComponent} from './hr/add-jd/add-jd.component';
import {EditJDComponent} from './hr/edit-jd/edit-jd.component';
import { ResumeDetailsComponent } from './hr/resume-details/resume-details.component';
import { SkillsetComponent } from './hr/skillset/skillset.component'; 

/**vendor */

import { VendorRegistrationComponent } from './vendor/vendor-registration/vendor-registration.component';
import { VendorViewComponent } from './vendor/vendor-view/vendor-view.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { RegisteredPageComponent } from './vendor/registered-page/registered-page.component'

/**Admin */
import { UserDetailsComponent } from './admin/user-details/user-details.component';
import { ApprovalComponent } from './admin/vendor-approval/vendor-approval.component';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { MailComposerComponent } from './admin/mail-composer/mail-composer.component'
import { SettingsComponent}  from './admin/settings/settings.component';
import { LicenserenewComponent }  from './admin/licenserenew/licenserenew.component';

import { AuthGuardGuard } from './Guard/auth-guard.guard';
import { LicGuard } from './Guard/lic.guard';

const appRoutes: Routes = [
      
        /**shared */
        {
          path: 'Home',
          component: HomeComponent,
          data: { title: 'Home' }
        },
        {
          path: '',
          redirectTo:'/Home',
          pathMatch:'full'
        },
		  {
          path: 'PrivacyPolicy',
         component:PrivacypolicyComponent,
         data: { title: 'Privacy Policy' }
        },
        {
          path:'Login',
          component:LoginComponent,
          canActivate: [LicGuard]
        },
        {
          path:'Logout',
          component:LoginComponent,
          canActivate: [LicGuard]
        },
        {
          //path:'Registration/:id1/:id2',
          path:'Registration',
          component:RegistrationComponent,
          canActivate: [LicGuard]
        },
        {
         // path:'Dashboard/:id1/:id2',
          path:'Dashboard',
          component:DashboardComponent,
          canActivate: [LicGuard,AuthGuardGuard]
        },
        {
         // path:'PublishedJD/:id1/:id2',
          path:'PublishedJD',
          component:PublishedComponent,
          canActivate: [LicGuard,AuthGuardGuard]
        },
        {
         // path:'ClosedJD/:id1/:id2',
         path:'ClosedJD',
          component:ClosedComponent,
          canActivate: [LicGuard,AuthGuardGuard]
        },
        /**Hr */
        {
         // path:'Add/:id1/:id2',
         path:'Add',
          component:AddJDComponent,
          canActivate: [LicGuard,AuthGuardGuard]

        },
        {
         // path:'Edit/:id/:id1/:id2',
          path:'Edit/:id',
          component:EditJDComponent,
          canActivate: [LicGuard,AuthGuardGuard]

        },
        /**JD  Details for HR */
        {
         // path: 'YourJD/:id1/:id2',
         path: 'YourJD',
          component: ResumeDetailsComponent,
          data: { title: 'Details' },
          canActivate: [LicGuard,AuthGuardGuard]
        },

        {
          path: 'Skillset Dictionary',
          component:SkillsetComponent,
          canActivate: [LicGuard,AuthGuardGuard]
        },
        {
          path: 'CandidateReport',
          component:CandidatereportComponent,
          canActivate: [LicGuard,AuthGuardGuard]
        },
        
        
  /** Vendor */
        {
          path: 'VendorRegistration',
          component: VendorRegistrationComponent,
          data: { title: 'Vendor' },
          canActivate: [LicGuard]
        },
        {
          //path: 'View/:id1/:id2',
          path: 'View',
          component: VendorViewComponent,
          data: { title: 'Vendor' },
          canActivate: [LicGuard,AuthGuardGuard]
        }, 
        /**Resume upload by Vendor */
        {
         // path: 'ResumeUpload/:id/:id1/:id2',
         path: 'ResumeUpload/:id',
          component: FileuploadComponent,
          data: { title: 'Upload' },
          canActivate: [LicGuard,AuthGuardGuard]
        },
        {
          path: 'Registered',
          component: RegisteredPageComponent,
          data: { title: 'Registered' },
          canActivate: [LicGuard]
        },
        /** Admin*/
        {
          //path: 'UserDetails/:id1/:id2/:id3',
          path: 'UserDetails/:id1',
          component:UserDetailsComponent,
          canActivate: [LicGuard,AuthGuardGuard]
        },
        {
          //path: 'VendorApproval/:id1/:id2',
          path: 'VendorApproval',
          component:ApprovalComponent,
          canActivate: [LicGuard,AuthGuardGuard]
        },
        {
          //path: 'MailComposer/:id1/:id2/:id3/:id4',
          path: 'MailComposer/:id1/:id2/:id3',
          component:MailComposerComponent,
          canActivate: [LicGuard,AuthGuardGuard]
        },
        {
         // path: 'EditProfile/:id1/:id2',
          path: 'EditProfile',
          component:ProfileComponent,
          canActivate: [LicGuard,AuthGuardGuard]
        },
        {
          path: 'License',
          component:LicenseComponent,

        },
        {
          path: 'ForgotPassword',
          component:ForgotpasswordComponent,
          canActivate: [LicGuard]
        },
        {
          path: 'ResetPassword/:id1/:id2',
          component:ResetPasswordComponent,
          canActivate: [LicGuard]
        },
        {
          path: 'Metrics',
          component:MetricsComponent,
          canActivate: [LicGuard,AuthGuardGuard]
        },
        {
          path: 'Settings',
          component:SettingsComponent,
          canActivate: [LicGuard,AuthGuardGuard]
        }
        ,
        {
          path: 'Search',
          component:SearchComponent,
          canActivate: [LicGuard,AuthGuardGuard]
        },
        
        {
          path: 'LicenseRenewal',
          component: LicenserenewComponent,
          canActivate: [LicGuard,AuthGuardGuard]
          
        },
        

];
@NgModule({
  declarations: [
    AppComponent,
    /** Shared */
    HomeComponent,
    LoginComponent,
    RegistrationComponent,
    DashboardComponent,
    PublishedComponent,
    ClosedComponent,
    HeaderComponent,
    FooterComponent,
    ProfileComponent,
    ForgotpasswordComponent,
    ResetPasswordComponent,
    MetricsComponent,
    SearchComponent,
    CandidatereportComponent,
    /** Hr */
    AddJDComponent,
    EditJDComponent,
    ResumeDetailsComponent,
    SkillsetComponent,

    /**Vendor */
    FileuploadComponent,
    VendorRegistrationComponent,
    VendorViewComponent,
    RegisteredPageComponent,
    
    /**Admin */
    UserDetailsComponent,
    ApprovalComponent,
    MailComposerComponent,
    SettingsComponent,
    LicenserenewComponent,
    
  ],
  imports: [
  
    //RouterModule.forRoot(routes, {useHash: true}),
    BrowserModule,
    NgxUiLoaderModule,
    AppRoutingModule,
    HttpClientModule,
    CommonModule,
    RouterModule,
    MatButtonModule,
    MatMenuModule,
     ReactiveFormsModule,
     FormsModule,
     CKEditorModule,
     ToastrModule.forRoot( {positionClass: 'toast-bottom-right'}),
     NgxWebstorageModule.forRoot(),
	 NgMultiSelectDropDownModule.forRoot(),     
	 NgIdleKeepaliveModule.forRoot(),
	 MomentModule,
	 NgxPaginationModule,
	 ModalModule,
     RouterModule.forRoot(
      appRoutes,
      {
        enableTracing:true,
		useHash: true
      }
    ),
     
     
     BrowserAnimationsModule,
     
     
     NgbModule
  

  ],
  exports:[RouterModule],
  providers: [
    { provide: HTTP_INTERCEPTORS,
      useClass: HttperrorInterceptor ,
      multi:true,
     },
     
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
