//function to get the jd details
getjddetail(id: any) {

  console.log("Am here1");
    this.http.get(environment.api_Url+'ViewJDbyid/' + id,{
      headers:{"Authorization":this.httpheaders}
     }).subscribe(data => {
        this.jddata = (data);
        this.jddata.id = (data[0].JDID);
        this.jddata.hr = (data[0].HRExecutiveID);
		if(data[0].jdDoc==null||data[0].jdDoc==""){
			this.jdflag=false;
		}
		

    });
    this.http.get(environment.api_Url+'getusernamebyjdid/' + id,{
      headers:{"Authorization":this.httpheaders}
     }).subscribe(data => {
      this.username = data;
      //this.Imagesrc = data[0].Image;
      this.Imagesrc=environment.api_Url+data[0].Image;
  });

}



//function to check the logged in user role
checkuserrole(id: any)
{
  this.ngxService.start();
  if (id == 3) {
      this.vendorrole = false;
      this.hrrole = true;
      this.upload = true;
      this.http.get(environment.api_Url+'viewresumesbyhr/' + this.jdid,{
        headers:{"Authorization":this.httpheaders}
       }).subscribe(data => {
         
          this.details = data;
          if (this.details.length == 0) {
            this.message = "You have no resume for this job"
        }
        for (var i=0; i<Object.keys(data).length ;i++)
        {
          if(this.details[i].ResumeScore==null)
          {
            this.details[i].ResumeScore=0;
          }
        }
        if(this.details.ResumeScore==null)
		 if (Object.keys(data).length > 10)
			  {this.paginateflag=true;}
		     else { this.paginateflag=false;}
      });
  }
  else if (id == 4) {
      this.vendorrole = true;
      this.hrrole = false;
      this.http.get(environment.api_Url+'viewvendorid/' + this.userid,{
        headers:{"Authorization":this.httpheaders}
       }).subscribe(data => {
        
        this.vendor = data;
        this.vendor[0].vid = (data[0].VendorID);
        this.http.get(environment.api_Url+'viewresumebyjdandcreatedby/' + this.jdid + '/' + this.vendor[0].vid,{
          headers:{"Authorization":this.httpheaders}
         }).subscribe(data => {
            this.details = (data);
            if (this.details.length == 0) {
              this.message = "You have no candidates for this job"
          }
          for (var i=0; i<Object.keys(data).length ;i++)
        {
          if(this.details[i].ResumeScore==null)
          {
            this.details[i].ResumeScore=0;
          }
        }
		   if (Object.keys(data).length > 10)
			  {this.paginateflag=true;}
		     else { this.paginateflag=false;}
        });
      });

  }
  else if (id == 1 || id == 2) {
      this.adminrole = true;
      this.http.get(environment.api_Url+'viewresumesbyhr/' + this.jdid,{
        headers:{"Authorization":this.httpheaders}
       }).subscribe(data => {
        
          this.details = (data);
          console.log(this.details);
          if (this.details.length == 0) {
            this.message = "You have no candidates for this job"
          }
          for (var i=0; i<Object.keys(data).length ;i++)
        {
          if(this.details[i].ResumeScore==null)
          {
            this.details[i].ResumeScore=0;
          }
        }
		   if (Object.keys(data).length > 10)
			  {this.paginateflag=true;}
		     else { this.paginateflag=false;}
        });
  }   this.ngxService.stop();
}
 
