 //function to save form information of resume applicant
public saveDatanew(form: NgForm) 
{
  if(this.DOB<= this.maximumDate && (this.DOB>=this.minimumDate))
  {
    console.log(this.interviewavail);
    if(this.interviewavail==null||
       (this.interviewavail!=null && (this.interviewavail>=this.interviewmindate ) && (this.interviewavail<this.interviewmaxdate) ) ) 
    {
      var ext = this.selectedFile[0].name.substr(this.selectedFile[0].name.lastIndexOf('.')+1);
      console.log("extension is"+ext);
      this.ngxService.start();
      console.log(typeof(this.relexp));
      console.log(typeof parseInt(this.relexp));
  if( parseInt(this.relexp)<=parseInt(this.totalexp)) 
  {
      if(this.validFormats.indexOf(ext) == -1){
          this.notifyService.showError("File can be of type doc/docx/pdf", "TALNET");
      }else if (this.filesize <= 2000000) 
      {
      
       //call the api to check for fake or blaclkisted profile
       
      this.http.post(environment.api_Url+'checkfake',form.value,{
        headers:{"Authorization":this.httpheaders}
       }).subscribe(data => {
            this.data1=(data);
            console.log(data);
             if(this.data1.length>0){

              this.notifyService.showError("This Resume is already marked as Fake/Blacklisted!", "TALNET");
              }
              else{
                  const uploadResumeData = new FormData();
                  uploadResumeData.append('name', this.personname);
                  uploadResumeData.append('gender', this.gender);
                  uploadResumeData.append('DOB', this.DOB);

                  uploadResumeData.append('mobile', this.mobile);
                  uploadResumeData.append('email', this.email);
                  uploadResumeData.append('skills', this.skills);
                  uploadResumeData.append('totalexp', this.totalexp);
                  uploadResumeData.append('relexp', this.relexp);
                  uploadResumeData.append('ctc', this.ctc);
                  uploadResumeData.append('ectc', this.ectc);
                  uploadResumeData.append('notp', this.notp);
                  uploadResumeData.append('location', this.location);
                  uploadResumeData.append('sellingprice', this.sellingprice);
                  uploadResumeData.append('id', this.jddata.id);
                  uploadResumeData.append('vid', this.vendor[0].vid);
                  uploadResumeData.append('hr', this.jddata.hr);
                  uploadResumeData.append('createdby', this.vendor[0].vid);
                  uploadResumeData.append('curcompany', this.curcompany);
                  uploadResumeData.append('preflocation', this.preflocation);
                  uploadResumeData.append('relocateflag', this.relocateflag);
                  uploadResumeData.append('yearpassing', this.yearpassing);
                  uploadResumeData.append('highestedu', this.highestedu);
                  uploadResumeData.append('offers', this.offers);
                  uploadResumeData.append('interviewavail', this.interviewavail);

                  uploadResumeData.append('resName', this.selectedFile[0], this.selectedFile[0].name);
                  uploadResumeData.append('userid', this.userid);
                  
                // this.addresume.nativeElement.disabled="true";
                  this.http.post(environment.api_Url+'resumeDataupload',uploadResumeData,{
                    headers:{"Authorization":this.httpheaders}
                  }).subscribe(res => {
                          
                          this.notifyService.showSuccess("Resume Uploaded Successfully.", "TALNET") ;
                          this.modalClose.nativeElement.click();
                          this.refresh();
                          // window.location.reload();
                    },err=>{
                      console.log(err);
                        this.notifyService.showError(err, "TALNET");
                        
                  });
                }
                 } ,error=>{
                    console.log(error);
                      this.notifyService.showError(error, "TALNET");
                      
                    }
                    
        );
      }
    
      else {
           
      this.notifyService.showError("File cannot be more than 2MB.", "TALNET");
      }
  }
  else if( parseInt(this.relexp)>parseInt(this.totalexp)){
    this.notifyService.showError("Relevant Experience cannot be more than Total Experience", "TALNET");
  }this.ngxService.stop();
  } else
    {
        this.notifyService.showError("Invalid Interview Date- Date should be from today to 2 months", "TALNET");
    }  
  }else{
    this.notifyService.showError("Date of Birth is invalid", "TALNET");
  }
 }
