//function to open the modal
  open(content) {
  //  this.modalReference = this.modalService.open(content);
    this.modalService.open(content, { ariaLabelledBy: 'modal-basic-title' }).result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
  }

//function to dismiss the modal
  private getDismissReason(reason: any): string {
   this.comments="";
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return `with: ${reason}`;
    }
  }
//function to delete the resume by vendor
  deleteResume(rid: any) {
    var conf = confirm("Are you sure you want to delete?");
    if (conf == true) {
      this.http.post(environment.api_Url+'delete_resume', { rid: rid ,userid:this.userid},{
        headers:{"Authorization":this.httpheaders}
       }).subscribe(data => {
          //window.location.reload();
          this.notifyService.showSuccess("Resume deleted.", "TALNET");
		  this.refresh();
        }
          , (err) => {
            this.message = err;
          });
    }
    else {
      return false;
    }
}

savestatusComments(form: NgForm)
{
  const commentsData = new FormData();
  commentsData.append('jdid', this.jddata.id);
  commentsData.append('resumeid', this.resumeid);
  commentsData.append('status', this.resume_status);
  commentsData.append('comments', this.comments);
 
  //this.getDismissReason(ModalDismissReasons.ESC);
  //this.modalReference.close();
  this.http.post(environment.api_Url+'resumecomments', commentsData,{
    headers:{"Authorization":this.httpheaders}
   }).subscribe(res => {
          // this.notifyService.showSuccess("Comments Saved", "TALNET");
           this.comments_saved=true;
           /*if(this.resume_status==3){
            this.selected(this.resumeid);
           }*/
           //3,4,5,8
           switch (this.resume_status) {
             case 3:
                this.selected(this.resumeid);
                break;
             case 4:
                this.offered(this.resumeid);
                break;
             case 5:
                this.rejected(this.resumeid);
                break;
             case 8:
                this.resume_irrevelant(this.resumeid);
                break;
             case 9:
               this.fakestatus(this.resumeid);
               break;
            case 10:
                this.blacklistedstatus(this.resumeid);
                break;

             default:
               break;
           }

     },err=>{
       console.log(err);
       this.comments_saved=false;
        this.notifyService.showError(err, "TALNET");
       
       }
  );
}


