get flagsFormArray() {
  return this.form.controls.flags as FormArray;
}
 

 
private addCheckboxesToForm() {

  this.mail_fields.forEach(() => this.flagsFormArray.push(new FormControl(false)));
  
}
 
submit() {
  const selectedOrderIds = this.form.value.flags
    .map((checked, i) => checked ? 1 : 0)
   //.filter(v => v !== null);
 console.log("selectdeflags"+selectedOrderIds);

console.log("Before post method"+this.mail_fields[0].value);

//post api to update the flag values in database

this.http.post(environment.api_Url+'flagDataupload/'+this.jdid, 
{email:this.mail_fields[0].value,
  mobile:this.mail_fields[1].value,
  dob:this.mail_fields[2].value,
  education:this.mail_fields[3].value,
  yearpassing:this.mail_fields[4].value,
  relexp:this.mail_fields[5].value,
  preflocation:this.mail_fields[6].value,
  relocate:this.mail_fields[7].value,
  interviewavail:this.mail_fields[8].value,
  submiteddate:this.mail_fields[9].value,
  sourceddate:this.mail_fields[10].value,
  source:this.mail_fields[11].value,
  offers:this.mail_fields[12].value,
},{
  headers:{"Authorization":this.httpheaders}
 }).subscribe(res => {
         this.notifyService.showSuccess("Mail Fields set successfully", "TALNET") ;
         this.refresh();
      
   },err=>{
     console.log(err);
      this.notifyService.showError(err, "TALNET");
  }
);




}

