 import { Component, ElementRef, OnInit, ViewChild,Renderer2 } from '@angular/core';
import { NgForm } from '@angular/forms';
import { HttpClient } from '@angular/common/http';
import { Router, RouterModule, Routes } from '@angular/router';
import { NotificationService } from 'src/app/Service/notification.service';
import { environment } from 'src/environments/environment.prod';
//import { config_logo } from '../../../assets/clientConfig/clientConfig';
import  { NgxUiLoaderService }  from 'ngx-ui-loader';

@Component({
  selector: 'app-vendor-registration',
  templateUrl: './vendor-registration.component.html',
  styleUrls: ['./vendor-registration.component.css']
})
export class VendorRegistrationComponent implements OnInit {
  @ViewChild('addvendor') addvendor: ElementRef; 
  @ViewChild('RegisterForm') RegisterForm: ElementRef; 
  password:any;
  confirmpassword:any;
  firstname:any;
  lastname:any;
  mobile:any;
  email:any;
  address:any;
  selectedFile:File;
  imageName:File;
  files:number;
  city:any={};
  state:any={};
  selectedstate:any;
  selectedcountry:any;
  selectedcity:any;
  country:any={};
  filesize:number;
  conpername:any;
  vendorCname:any;
  message:String;
  stateid:number;
  cityno:number;
  logo_url:string;
  validFormats:any=['jpg','jpeg','png'];
  config:any={};
  uploadlogo:boolean;

  constructor(private http:HttpClient,private router:Router,private notifyService : NotificationService,private ngxService: NgxUiLoaderService,private renderer: Renderer2) { }

  ngOnInit():void {
    //this.logo_url=config_logo;
    //get countries for the form dropdown 
    this.http.get(environment.api_Url+'getallcountries').subscribe(data=>{
      this.country=(data);
  }); 
  //get states for the form dropdown
  this.http.get(environment.api_Url+'getallstates').subscribe(data=>{
    this.state=(data);
   });
   this.http.get(environment.api_Url+'getConfig/').subscribe(data => {
    // this.config=(data);
     this.config=data;
     console.log(this.config);
     this.config.Logo=(data[0].Logo);
     this.logo_url=environment.api_Url+this.config.Logo;
  });
 
}

/**function to set all the cities for the selected state**/
public onSelect(e) 
{
    var stateid=e;
    console.warn(stateid);
    this.stateid=stateid;
    this.http.get(environment.api_Url+'getallcities/'+stateid).subscribe(data=>{
        console.warn(data);
        this.city=(data);
    });
}

/** function change event on file select ***/
public onFileChanged(event){
  this.selectedFile = event.target.files;
  console.warn(this.selectedFile[0].name);
  this.filesize=this.selectedFile[0].size;
   console.warn(this.filesize);
   this.uploadlogo=true;

}

/** function collect form data for vendor registration ***/
collectData(form:NgForm)
{
  if(this.uploadlogo==true)
  {
  var ext = this.selectedFile[0].name.substr(this.selectedFile[0].name.lastIndexOf('.')+1);
   console.log("extension is"+ext);
  if(this.validFormats.indexOf(ext) == -1){
       this.notifyService.showError("Logo File can be of type jpg,jpeg or png", "TALNET");
  }else if(this.filesize<=100000)
    {
	  if(this.password==this.confirmpassword)
      {
        this.ngxService.start();
        const uploadImageData = new FormData();
      uploadImageData.append('vendorCname',this.vendorCname);
      uploadImageData.append('imageName',this.selectedFile[0],this.selectedFile[0].name);
      uploadImageData.append('conpername',this.conpername);
      uploadImageData.append('mobile',this.mobile);
      uploadImageData.append('email',this.email);
      uploadImageData.append('password',this.password);
      uploadImageData.append('address',this.address);
      uploadImageData.append('country',this.selectedcountry);
      uploadImageData.append('city',this.selectedcity);
      uploadImageData.append('state',this.selectedstate);
      this.addvendor.nativeElement.disabled="true";
     //this.addvendor.nativeElement.addclass('btndisabled');
      this.http.post(environment.api_Url+'AddVendorwithlogo',uploadImageData).subscribe(res => 
      {
        //alert("You are successfully Registered.");
        this.router.navigate(['Registered']);
        this.notifyService.showSuccess("You are successfully Registered", "TALNET");
        //window.location.reload();
   
      },(error) => {                              //Error callback
          console.log(error);
          //this.message=error;
          this.renderer.removeAttribute(this.addvendor.nativeElement,'disabled');
          this.notifyService.showError(error, "TALNET");

      });
      this.ngxService.stop();
      this.renderer.removeAttribute(this.addvendor.nativeElement,'disabled');
	 }else
	   {
   //   this.renderer.removeAttribute(this.addvendor.nativeElement,'disabled');
     this.notifyService.showError("Passwords do not match!", "TALNET");

	   }
   }else
    {  
      //this.renderer.removeAttribute(this.addvendor.nativeElement,'disabled');
       this.notifyService.showError("Logo file  cannot be more than 100 KB", "TALNET");
       

    }
  }
  else{
    if(this.password==this.confirmpassword)
      {this.addvendor.nativeElement.disabled="true";
        this.ngxService.start();
       
       //this.renderer.setAttribute(this.addvendor.nativeElement,'disabled','true');
        this.http.post(environment.api_Url+'AddVendor',form.value).subscribe(res => 
        {
          //alert("You are successfully Registered.");
          this.router.navigate(['Registered']);
          this.notifyService.showSuccess("You are successfully Registered", "TALNET");
          //window.location.reload();
     
        },(error) => {                              //Error callback
            console.log(error);
            //this.message=error;
           this.renderer.removeAttribute(this.addvendor.nativeElement,'disabled');
          this.notifyService.showError(error, "TALNET");
        });
        this.ngxService.stop();
        
         this.renderer.removeAttribute(this.addvendor.nativeElement,'disabled');

     }else
       {
      //this.renderer.removeAttribute(this.addvendor.nativeElement,'disabled');
      this.notifyService.showError("Passwords do not match!", "TALNET");
       }

      
  }
   
}


 
}
