 import { Component, OnInit,ViewChild } from '@angular/core';
 import { DetailsService } from 'src/app/Service/details.service';
 import { ActivatedRoute,Router } from '@angular/router';
 import { HttpClient } from '@angular/common/http';
 import { NgForm } from '@angular/forms';
 import { formatCurrency } from '@angular/common';
 import { LocalStorageService } from 'ngx-webstorage';
 import { environment } from 'src/environments/environment.prod';
 import { NotificationService } from 'src/app/Service/notification.service';
 import { ModalDismissReasons, NgbModal, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
 

@Component({
  selector: 'app-skillset',
  templateUrl: './skillset.component.html',
  styleUrls: ['./skillset.component.css'],
  providers:[DetailsService],
})
export class SkillsetComponent implements OnInit {

  @ViewChild('addskill') addskill: any;
   @ViewChild('myModalClose') modalClose;
   @ViewChild('updateClose') updateClose;
   @ViewChild('update') modalupdate: any; 
   modalReference: NgbModalRef;
  closeResult: String;
   user;
   CloseJDForm:NgForm;
   userid:any;
   userroleno:any;
   userdetails:any;
   userrole:boolean;
   Edit:any={};
   skillset:any;
   synonyms:any;
   equivalent_skills:any;
   errmessage:any;
   skillname:any;
   skill_name:any;
   status:boolean;
   showdata:boolean;
   title:boolean;
   httpheaders:string;
   cp: number = 1;
   paginateflag:Boolean;

   constructor(private modalService: NgbModal,private http:HttpClient,private route:ActivatedRoute,private router:Router,private service:DetailsService,private localSt:LocalStorageService,private notifyService : NotificationService){
         
  }
   ngOnInit(): void {
     //fetching user details 
     
     this.user=this.localSt.retrieve('user');
      this.userid=this.user[0].userid
      this.userroleno=this.user[0].userrole;
      const idToken = this.user[1].accesstoken;
      this.httpheaders="Bearer " + idToken;
      this.checkuserrole(this.userroleno);
       this.getskills();
 
 
  }
  
 //check the user role for HR
    checkuserrole(id:any)
    {
      if(id==4){
       this.userrole=false;
      }
      else{
       this.userrole=true;
      }
    }

//function to fetch all the skills
  getskills()
  {
    this.http.get(environment.api_Url+'getallskills',{
      headers:{"Authorization":this.httpheaders}
     }).subscribe(data=>{
              this.skillset=data;
              if(this.skillset.length==0)
              {
                this.errmessage=true;
                this.showdata=false;
                this.title=false;
             
              }
              else{
                this.showdata=true;
                this.title=true;
                this.errmessage=false;
                if (Object.keys(data).length > 10)
                {this.paginateflag=true;}
               else { this.paginateflag=false;}
              }  
            
      });
  }
  
  //function to fetch the report of selected dates
  saveData(form:NgForm)
  {

    this.http.post(environment.api_Url+'addskillset',form.value,{
      headers:{"Authorization":this.httpheaders}
     }).subscribe(response=> 
     {
      if (response) 
      {
      
        this.notifyService.showSuccess("New Skillset is succesfully added", "TALNET");
        //this.router.navigate(['/Dashboard']);
        this.modalClose.nativeElement.click();
              this.refresh();
      } 
      else 
      {
        
        this.notifyService.showError("Problem in adding the Skillset", "TALNET");
      }      
    });

  }
  delete(sid:any)
  {
    var conf = confirm("Are you sure you want to delete?");
    if (conf == true) 
    {
      this.http.post(environment.api_Url+'deleteskillset', { sid: sid },{
  //  this.http.post(environment.api_Url+'deleteskillet',,{
      headers:{"Authorization":this.httpheaders}
     }).subscribe(response=> 
     {
      if (response) 
      {
      
        this.notifyService.showSuccess("Skillset deleted", "TALNET");
        //this.router.navigate(['/Dashboard']);

        this.refresh();
      } 
      else 
      {
        
        this.notifyService.showError("Problem in deleting the Skillset", "TALNET");
      }      
    });
   }
  }


  edit(sid:any)
  {
    this.http.get(environment.api_Url+'getskills/'+sid,{
      headers:{"Authorization":this.httpheaders}
     }).subscribe(data=>{
            
              this.Edit=data;
            console.log(this.Edit);
           this.Edit.id=(data[0].skill_id);
            this.Edit.skill_name=(data[0].skill);
            this.Edit.synonyms=(data[0].synonyms);
            this.Edit.equivalent_skills=(data[0].secondary_level_skillset);
            
      });
    //  this.modalupdate.open();
  }
  //function to open the modal
  open(content) {
    //  this.modalReference = this.modalService.open(content);
      this.modalService.open(content, { ariaLabelledBy: 'modal-basic-title' }).result.then((result) => {
        this.closeResult = `Closed with: ${result}`;
      }, (reason) => {
        this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
      });
    }

  //function to dismiss the modal
  private getDismissReason(reason: any): string {
    
     if (reason === ModalDismissReasons.ESC) {
       return 'by pressing ESC';
     } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
       return 'by clicking on a backdrop';
     } else {
       return `with: ${reason}`;
     }
   }
  updatedata(sid:any,form: NgForm)
  {

   /* const skillsetupdate = new FormData();
    skillsetupdate.append('sid',sid);
    skillsetupdate.append('skill_name', this.skill_name);
    skillsetupdate.append('synonyms', this.synonyms);
    skillsetupdate.append('equivalent_skill', this.equivalent_skills);

*/
    this.http.post(environment.api_Url+'updateskillset/'+sid, form.value,{
      headers:{"Authorization":this.httpheaders}
     }).subscribe(res => {
      this.updateClose.nativeElement.click();
             this.notifyService.showSuccess("Skillset Updated Successfully.", "TALNET") ;
         
            this.refresh();
       
       },err=>{
         console.log(err);
          this.notifyService.showError(err, "TALNET");
          
         }
    );

  }
  refresh() {
    const prev = this.router.routeReuseStrategy.shouldReuseRoute;
    const prevOSN = this.router.onSameUrlNavigation;
    this.router.routeReuseStrategy.shouldReuseRoute = () => false;
    this.router.onSameUrlNavigation = 'reload';
  
    this.router.navigate(['Skillset Dictionary/']);
    setTimeout(() => {
      this.router.routeReuseStrategy.shouldReuseRoute = prev;
      this.router.onSameUrlNavigation = prevOSN;
    }, 0);
  }
}
