import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { NgForm } from '@angular/forms';
import { HttpClient } from '@angular/common/http';
import { ActivatedRoute,Router } from '@angular/router';
import { RouterModule, Routes } from '@angular/router';
import { DetailsService } from 'src/app/Service/details.service';
import { NotificationService } from 'src/app/Service/notification.service';
import { LocalStorageService } from 'ngx-webstorage';
import { environment } from 'src/environments/environment.prod';

@Component({
  selector: 'app-registration',
  templateUrl: './registration.component.html',
  styleUrls: ['./registration.component.css']
})
export class RegistrationComponent implements OnInit {
  @ViewChild('adduser') adduser: ElementRef;   
  user;
  userid:any;
  selectedFile:File;
  imageName:File;
  files:number;
  userroleno:any;
  loginid:any;
  password:any;
  firstname:any;
  lastname:any;
  mobile:any;
  email:any;
  userrole:any;
  selecteduserrole:any;
  status:number;
  options:any=[];
  message:string;
  alert:any;
  filesize:number;
  details:any;
  x:any;
  httpheaders:string;
  validFormats:any=['jpg','jpeg','png'];
  addformhide:Boolean;
  maxusers:any;
  createdusers:any
  constructor(private http:HttpClient,private route:ActivatedRoute,private router:Router,private notifyService : NotificationService,private localSt:LocalStorageService) { }

  ngOnInit(): void {
   
    this.user=this.localSt.retrieve('user');
    this.userid=this.user[0].userid
    this.userroleno=this.user[0].userrole;
    const idToken = this.user[1].accesstoken;
    this.httpheaders="Bearer " + idToken;
    this.http.get(environment.api_Url+'getuserrole',{
      headers:{"Authorization":this.httpheaders}
     }).subscribe(data=>{
        this.options=(data);
      });
      this.usercount();
  }
  

  //function for company logo file upload event
  public onFileChanged(event){
    this.selectedFile = event.target.files;
    console.warn(this.selectedFile[0].name);
    this.filesize=this.selectedFile[0].size;
     console.warn(this.filesize);
 }

 //function to upload the data of new user
collectData(form:NgForm)
 {
    var ext = this.selectedFile[0].name.substr(this.selectedFile[0].name.lastIndexOf('.')+1);
   console.log("extension is"+ext);
  if(this.validFormats.indexOf(ext) == -1){
       this.notifyService.showError("File can be only jpg, jpeg or png", "TALNET");
  }else{
     if(this.filesize<=100000)
    {
      
     const uploadImageData = new FormData();
      uploadImageData.append('firstname',this.firstname);
      uploadImageData.append('lastname',this.lastname);
      uploadImageData.append('imageName',this.selectedFile[0],this.selectedFile[0].name);
      uploadImageData.append('mobile',this.mobile);
      uploadImageData.append('email',this.email);
      uploadImageData.append('password',this.password);
      uploadImageData.append('userrole',this.selecteduserrole);
      uploadImageData.append('userid',this.userid);
      //this.adduser.nativeElement.disabled="true";
      this.http.post(environment.api_Url+'AddUser',uploadImageData,{
        headers:{"Authorization":this.httpheaders}
      }).subscribe(res => 
        {
            this.notifyService.showSuccess("User Added Successfully.", "TALNET");
            window.location.reload();

        },(err)=>
		{     this.notifyService.showError(err,'TALNET');
		});
    }
     else{
        this.notifyService.showError("Image size should be less than 100KB", "TALNET");
     }
   }
 }
 usercount()
 {
   //1.call api license table for max users
   //2.call api user table where usersrole is 2or3 and status=1
   //if(step2>1) block form 
   console.log("inside usercount");
    this.http.get(environment.api_Url+'getlicensedata',{
      headers:{"Authorization":this.httpheaders}
     }).subscribe(result=>{
      console.log(result);
        this.maxusers=result[0].NoUsers;
        console.log(result[0].NoUsers);
      
      this.http.get(environment.api_Url+'getactiveusercount',{
        headers:{"Authorization":this.httpheaders}
       }).subscribe(result1=>{
        console.log(result1);
         this.createdusers=result1[0].activeusers;
            
        console.log("Am here now")
       console.log(this.createdusers+this.maxusers);
        if(this.createdusers >=  this.maxusers)
        { 
          this.addformhide=true;
          console.log("form to be hidden");
        }else{
          this.addformhide=false;
        }
      },error=>{
        this.notifyService.showError(error,'TALNET');
      });

    },error=>{
      this.notifyService.showError(error,'TALNET');
    });
 }
}//end of regisration component file
