import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ActivatedRoute,Router } from '@angular/router';
import { DetailsService } from 'src/app/Service/details.service';
import {LocalStorageService} from 'ngx-webstorage';
import { environment } from 'src/environments/environment.prod';
import { config_logo } from '../../../assets/clientConfig/clientConfig';
import { AppServiceService } from '../../Service/app-service.service';
import { NgxUiLoaderService } from 'ngx-ui-loader';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css'],
  providers:[DetailsService]
})
export class HeaderComponent implements OnInit {
  user;
  userdetails:any;
  Details:any={};
  userrole:boolean;
  hrrole:boolean;
  vendorrole:boolean;
  adminrole:boolean;
  managerrole:boolean;
  userid:number;
  userroleno:number;
  rolename:any;
  httpheaders:string;
  Imagesrc:any;
  logo_url:string;
  admin:boolean;
  config:any={};
  constructor(private http:HttpClient,private route:ActivatedRoute,private router:Router,private service:DetailsService,private localSt:LocalStorageService,private appService:AppServiceService,private ngxService: NgxUiLoaderService) { }

  ngOnInit(): void {
    //fetech user data
	//this.logo_url=config_logo;
  this.http.get(environment.api_Url+'getConfig/').subscribe(data => {
    // this.config=(data);
     this.config=data;
     console.log(this.config);
     this.config.Logo=(data[0].Logo);
     this.logo_url=environment.api_Url+this.config.Logo;
  });
    this.user=this.localSt.retrieve('user');
    this.userid=this.user[0].userid
    this.userroleno=this.user[0].userrole;
    const idToken = this.user[1].accesstoken;
    this.httpheaders="Bearer " + idToken;
    this.ngxService.start();
    this.getDetail(this.userroleno);

    this.service.getuserdetailsbyid(this.userid,this.userroleno).subscribe((result)=>{

      
       this.userdetails=result;
        this.Imagesrc=environment.api_Url+result[0].Image;
        console.log(this.Imagesrc);
     });
    this.http.get(environment.api_Url+'getuserroleforid/'+this.userroleno,
     {
       headers:{"Authorization":this.httpheaders}
       }).subscribe(data=>
      {
           this.rolename=data[0].RoleName;
          if(this.rolename=="HR")
          {
            this.rolename="HR/Recruiter"
          }
          else if(this.rolename=="Vendor")
          {
            this.rolename="Vendor/Freelancer"
          }
          else if(this.rolename=="Management")
          {
            this.rolename="BO Head"
          }
          if(this.userroleno==1)
          {
            this.admin=true;
          }
      });
      this.ngxService.stop();

}

logo_redirect()
{
 if(this.appService.getUserLoggedIn())
 {
  this.router.navigate(['Dashboard']);
 }else{
  this.router.navigate(['Home']);
 }
}
  
  getDetail(id:any)
  {
    
      if(id==3)
      {
        this.hrrole=true;
   }
      else if(id==4){
        this.vendorrole=true;
     }
      else if(id==1)
      {
        this.adminrole=true;
     }
      else if(id==2){
        this.managerrole=true;
        
      }
  }
  
  logout()
  {
    this.localSt.clear('user');
    localStorage.setItem('loginStatus','0');
    console.log("The user logout status is "+localStorage.getItem('loginStatus'));
    this.appService.setUserLoggedIn(false);
    //this.router.navigate(['Logout']);
    this.router.navigate(['Home']);
  }

}//end of header component file
