generatemetrics()
{
  this.metrics=true;
  this.cp=0;
  this.totalflag=0;
   this.inprogressflag=0;
   this.completedflag=0;
   this.cancelledflag=0;
   this.appliedflag=0;
   this.metricflag=1;
   this.ngxService.start();
   if(this.userroleno==4)
   {
    this.vendorrole=true;
    this.http.get(environment.api_Url+'viewvendorid/' +this.userid,{
      headers:{"Authorization":this.httpheaders}
     }).subscribe(data=>{
        this.vendors=data;
        this.vendors[0].vid=(data[0].VendorID);
        this.http.get(environment.api_Url+'getvendormetrics/'+this.vendors[0].vid,{
           headers:{"Authorization":this.httpheaders}
          }).subscribe(data1=>{
          //  this.ngxService.stop();
            this.Metrics=(data1[0]);
            this.showMetrics(this.Metrics);

          });

        });
    }else if(this.userroleno==1 ||this.userroleno==2)
    {
      this.http.get(environment.api_Url+'getadminmetrics',{
        headers:{"Authorization":this.httpheaders}
       }).subscribe(data1=>{
       // this.ngxService.stop();
         this.Metrics=(data1[0]);
         this.showMetrics(this.Metrics);


       });
    }
    else if(this.userroleno==3)
    {
      this.http.get(environment.api_Url+'gethrmetrics/'+this.userid,{
        headers:{"Authorization":this.httpheaders}
       }).subscribe(data1=>{
        //this.ngxService.stop();
         this.Metrics=(data1[0]);
         this.showMetrics(this.Metrics);

       });
    } 
    this.ngxService.stop();
    

}
showMetrics(Metrics:any)
{

         if(this.Metrics.length==0){
          this.metricsmessage="You have no data to display"
          this.metricsshow=true;
        }
        else{
          this.metricsshow=false;
  console.log("Zero Metrics"+this.Metrics.length);
  for(var i=0;i<Object.keys(this.Metrics).length;i++)
  {
    if(this.Metrics[i].vrt ==0) this.Metrics[i].vrt=0.5;
    if(this.Metrics[i].mvrt ==0) this.Metrics[i].mvrt=0.5;
    if(this.Metrics[i].hrt ==0) this.Metrics[i].hrt=0.5;
    if(this.Metrics[i].mhrt ==0) this.Metrics[i].mhrt=0.5;
    if(this.Metrics[i].crt ==0) this.Metrics[i].crt=0.5;
    if(this.Metrics[i].mcrt ==0) this.Metrics[i].mcrt=0.5;
    if(this.Metrics[i].vcost ==0) this.Metrics[i].vcost=0.5;
    if(this.Metrics[i].hcost ==0) this.Metrics[i].hcost=0.5;
  }
   if (Object.keys(this.Metrics).length > 10)
           {this.metricspaginateflag=true;}
           else { this.metricspaginateflag=false;}
           } 
}

 
