import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ActivatedRoute,Router } from '@angular/router';
import { NgForm } from '@angular/forms';
import { DatePipe } from '@angular/common';
import { DetailsService } from 'src/app/Service/details.service';
import { LocalStorageService } from 'ngx-webstorage';
import { environment } from 'src/environments/environment.prod';
import { NotificationService } from 'src/app/Service/notification.service';
import * as moment from 'moment';
import { ElementRef,ViewChild } from '@angular/core';

@Component({
  selector: 'app-edit-jd',
  templateUrl: './edit-jd.component.html',
  styleUrls: ['./edit-jd.component.css'],
  providers:[DatePipe,DetailsService]
})

export class EditJDComponent implements OnInit {
  @ViewChild('editjdbtn') editjdbtn: ElementRef;
  user;
  Edit:any={};
    summary:any;
  jobtitle:any;
  company:any;
  bunit:any;
  desc:any;
  skillset:any;
  education:any;
  mandareq:any;
  nicetohave:any;
  certi:any;
  category:any;
  location:any;
  Terms:any;
  hrexecid:any;
  postedon:any;
  validtill:any;
  exp_from:any;
  exp_to:any;
  createdby:number;
  message:string;
  userid:any;
  userroleno:number;
  userdetails:any;
  httpheaders:string;
  selectedFile: File;
  filesize: any;
  files: number;
  jdDoc: File;
  upload:Boolean;
  options:any;
  minimumdate:any;
  validFormats:any=['pdf','doc','docx'];
  positions:any;
  emptypeoptions: any;
  emptype:any;
  postypeoptions:any;
  postype:any;

  constructor(private route:ActivatedRoute,private http :HttpClient,private router:Router,private datePipe: DatePipe,private service:DetailsService,private notifyService : NotificationService,private localSt:LocalStorageService) { }

  ngOnInit(){
  //retrive logged in user data 
  
  this.user=this.localSt.retrieve('user');
  this.userid=this.user[0].userid
  this.userroleno=this.user[0].userrole;
  const idToken = this.user[1].accesstoken;
  this.httpheaders="Bearer " + idToken;
  this.minimumdate= moment().format("YYYY-MM-DD");
  this.emptypeoptions = [{ id: 1, name: "Contract" }, { id: 2, name: "FTE Hire " }];
  this.postypeoptions= [{ id: 1, name: "New Hire" }, { id: 2, name: "Replacement"}];
   
  //this.editjdbtn.nativeElement.disabled="false";
    this.service.getuserdetailsbyid(this.userid,this.userroleno).subscribe((result)=>{
      this.userdetails=result;
      console.warn(result);
    });

    this.getDetail(this.route.snapshot.params['id']);
    this.http.get(environment.api_Url+'getjdcategory').subscribe(data=>
      {
      this.options=(data);
      });

  
  
}


/**function to get the details of the jd */ 
getDetail(id:any)
{
  
  this.http.get(environment.api_Url+'editjd/'+id,{
            headers:{"Authorization":this.httpheaders}
           }).subscribe(data => {
            this.Edit=data;
            this.Edit.id=(data[0].JDID);
            this.Edit.summary=(data[0].Summary);
            this.Edit.category=(data[0].Category);
            this.Edit.jobtitle=(data[0].JobTitle);
            this.Edit.company=(data[0].Company);
            this.Edit.bunit=(data[0].BusinessUnit);
            this.Edit.location=(data[0].Location);
            this.Edit.desc=(data[0].Description);
            this.Edit.skillset=(data[0].SkillSet);
            this.Edit.education=(data[0].Education);
            this.Edit.mandareq=(data[0].MandatoryReq);
            this.Edit.nicetohave=(data[0].NicetoHave);
            this.Edit.certi=(data[0].Certification);
            this.Edit.hrexecid=(data[0].HRExecutiveID);
            this.Edit.positions=(data[0].Positions);
            this.Edit.postype=(data[0].Postype);
            this.Edit.emptype=(data[0].Emptype);
            this.Edit.validtill=this.datePipe.transform((data[0].ValidTill), 'yyyy-MM-dd');
            this.Edit.exp_from=(data[0].Exp_from);
            this.Edit.exp_to=(data[0].Exp_to);
            var jdfile= data[0].jdDoc.split("/",3);
           // this.Edit.jdDoc=jdfile[2];
            this.Edit.jdDoc=jdfile[2].split("-",1);
          });
}

/**function update jd with new form values*/
updatedata(id:any,form:NgForm)
{
  if(this.Edit.exp_from<=this.Edit.exp_to) 
  {
  if(this.upload!=true)
  {
    this.editjdbtn.nativeElement.disabled="true";
  this.http.put(environment.api_Url+'updatejd/'+id,form.value,{
    headers:{"Authorization":this.httpheaders}
   }).subscribe(data=>{
    this.notifyService.showSuccess("Changes Saved Successfully", "TALNET");
    this.router.navigate(['/Dashboard']);
   });
  }
  else{
    var ext = this.selectedFile[0].name.substr(this.selectedFile[0].name.lastIndexOf('.')+1);
    if(this.validFormats.indexOf(ext) == -1){
      this.notifyService.showError("File can be only pdf / doc/ docx", "TALNET");
    }else if (this.filesize <= 200000)
    {
      this.editjdbtn.nativeElement.disabled="true";
     const uploadjdData = new FormData();
     uploadjdData.append('category', this.Edit.category);
     uploadjdData.append('jobtitle', this.Edit.jobtitle);
     uploadjdData.append('company', this.Edit.company);
     uploadjdData.append('bunit', this.Edit.bunit);
     uploadjdData.append('location', this.Edit.location);
     uploadjdData.append('desc', this.Edit.desc);
     uploadjdData.append('skillset', this.Edit.skillset);
     uploadjdData.append('education', this.Edit.education);
     
     uploadjdData.append('mandareq', this.Edit.mandareq);
     uploadjdData.append('nicetohave', this.Edit.nicetohave);
     uploadjdData.append('certi', this.Edit.certi);
     uploadjdData.append('summary', this.Edit.summary);
     uploadjdData.append('Terms', this.Edit.Terms);
     //uploadjdData.append('postedon', this.Edit.postedon);
     uploadjdData.append('validtill', this.Edit.validtill);
     uploadjdData.append('createdby', this.Edit.userid);
     uploadjdData.append('hrexecid', this.Edit.hrexecid);
     uploadjdData.append('exp_from', this.Edit.exp_from);
      uploadjdData.append('exp_to', this.Edit.exp_to);
      uploadjdData.append('positions', this.positions);
      uploadjdData.append('postype', this.postype);
      uploadjdData.append('emptype', this.emptype);
     uploadjdData.append('userid', this.userid);
     uploadjdData.append('jdDoc', this.selectedFile[0], this.selectedFile[0].name);
     console.log("Load JD data:"+this.jdDoc);
     
     this.http.put(environment.api_Url+'updatejdDoc/'+id,uploadjdData,{
       headers:{"Authorization":this.httpheaders}
     }).subscribe(response=> 
         {
           if (response) 
       {
         this.message = 'JD Saved ';
         this.notifyService.showSuccess("Your JD is saved", "TALNET");
         this.router.navigate(['/Dashboard']);
       } 
       else 
       {
         this.message = 'Error';
         this.notifyService.showError("Problem in updating the JD", "TALNET");
       }      
         });
    }
    else {
     this.notifyService.showError("File should be less than 200KB.", "TALNET");
     }
   }
   } else
   {
    this.notifyService.showError("Experience From is more than to ", "TALNET");
   }
}

public onFileChanged(event) {
  console.log("Inside Event")
  this.selectedFile = event.target.files;
  console.warn(this.selectedFile[0].name);
  this.filesize = this.selectedFile[0].size;
  console.warn(this.filesize);
  this.files = event.target.files.length;
  console.warn(event.target.files.length);
  this.upload=true;
}


}
//end of edit jd component