 import { Component, OnInit } from '@angular/core';
 import { DetailsService } from 'src/app/Service/details.service';
 import { ActivatedRoute,Router } from '@angular/router';
 import { HttpClient } from '@angular/common/http';
 import {CommonModule} from '@angular/common';
 import { NgForm } from '@angular/forms';
 import { LocalStorageService } from 'ngx-webstorage';
 import { environment } from 'src/environments/environment.prod';
 import * as XLSX from 'xlsx';
 import { NgxUiLoaderService } from 'ngx-ui-loader';
 
 
@Component({
  selector: 'app-candidatereport',
  templateUrl: './candidatereport.component.html',
  styleUrls: ['./candidatereport.component.css'],
  providers:[DetailsService]
})
export class CandidatereportComponent implements OnInit {

  user;
  userid:any;
  userroleno:any;
  userdetails:any;
  hrrole:boolean;
  vendorrole:boolean;
  adminrole:boolean;
  managerrole:boolean;
  startdate:any;
  enddate:any;
  details:any;
  message:any;
  nodata:any;
  status:boolean;
  prevdate:any;
  date: Date = new Date();
  title30:boolean;
  title:boolean;
  httpheaders:string;
  cp: number = 1;
  paginateflag:Boolean;
  fileName= 'Report_CandidateVice.xlsx';
    constructor(private http:HttpClient,private route:ActivatedRoute,private router:Router,private service:DetailsService,private localSt:LocalStorageService,private ngxService: NgxUiLoaderService) { }
  
    ngOnInit(): void {
       this.user=this.localSt.retrieve('user');
       this.userid=this.user[0].userid;
       console.log("Userid"+this.userid)
       this.userroleno=this.user[0].userrole;
       const idToken = this.user[1].accesstoken;
       this.httpheaders="Bearer " + idToken;
  
       this.service.getuserdetailsbyid(this.userid,this.userroleno).subscribe((result)=>{
          this.userdetails=result;
          console.log("username:",this.userdetails);
   
        });
       this.checkuserrole(this.userroleno);
       this.prevdate=new  Date(Date.now() - 30 * 24 * 60 * 60 * 1000)
       this.startdate= this.prevdate.toISOString().substr(0, 10);
       this.enddate=this.date.toISOString().substr(0, 10);
       console.log(this.startdate);
       console.log(this.enddate);
       console.log(this.userroleno);
       this.title30=true;
       this.title=false;
  
       /***Init page loader */
        const initload = new FormData();
        console.log(this.startdate);
        initload.append('startdate',this.startdate);
        initload.append('enddate', this.enddate);
        this.ngxService.start();
        //load the report for vendor, admin,management
       if(this.userroleno==2||this.userroleno==1)
         { 
              this.http.post(environment.api_Url+'candidatereportmgmt',initload,{
                headers:{"Authorization":this.httpheaders}
               }).subscribe(data => {
                
                    this.details=data;
                    if(this.details.length==0)
                    {
                      this.status=true;
                      this.nodata="No Data available betweeen this dates";
                    }
                    else{
                      this.status=false;
                      this.message=true;
            if (Object.keys(data).length > 10)
            {this.paginateflag=true;}
           else { this.paginateflag=false;}
                    } 
           });
         }
          else if(this.userroleno==3)
          {  //load the report for HR -his own published JD 
              this.http.post(environment.api_Url+'candidatereportHR/'+this.userid,initload,{
              headers:{"Authorization":this.httpheaders}
             }).subscribe(data => {
              
                this.details=data;
                console.warn("hr details",this.details);
                if(this.details.length==0)
                {
                  console.log("here");
                  this.status=true;
                  this.nodata="No Data available betweeen this dates";
                }
                else{
                  this.status=false;
                  this.message=true;
          if (Object.keys(data).length > 10)
          {this.paginateflag=true;}
           else { this.paginateflag=false;}
                }
               });
          }
          this.ngxService.stop();
    }
    
   
     checkuserrole(id:any)
    {
      if(id==3)
     {
       this.hrrole=true
      }
      else if(id==4)
      {
        this.vendorrole=true
      }
      else if(id==2)
      {
        this.managerrole=true
      }
      else if(id==1)
      {
        this.adminrole=true
      }
   }
   resumeupload(id:any){   /*JDID**userid**userroleid*/
    this.router.navigate(['/ResumeUpload',id]);
    console.warn(this.userroleno);
  }
   //function to view published jd for admin,vendor,management
   collectData(form:NgForm)
   {
    this.title30=false;
    this.title=true;
    this.ngxService.start();
     if(this.userroleno==2||this.userroleno==1)
     {
            this.http.post(environment.api_Url+'candidatereportmgmt',form.value,{
              headers:{"Authorization":this.httpheaders}
             }).subscribe(data => {
             
                  this.details=data;
                  if(this.details.length==0)
                  {
                    this.status=true;
                    this.nodata="No Data available betweeen this dates";
                  }
                  else{
                    this.status=false;
                    this.message=true;
            if (Object.keys(data).length > 10)
            {this.paginateflag=true;}
           else { this.paginateflag=false;}
                  }
            });
     }
   
     else if(this.userroleno==3)
     {
        this.http.post(environment.api_Url+'candidatereportHR/' +this.userid,form.value,{
          headers:{"Authorization":this.httpheaders}
        }).subscribe(data => {
              
              this.details=data;
              console.warn("hr details",this.details);
              if(this.details.length==0)
              {
                this.status=true;
                this.nodata="No Data available betweeen this dates";
              }
              else{
                this.status=false;
                this.message=true;
          if (Object.keys(data).length > 10)
          {this.paginateflag=true;}
           else { this.paginateflag=false;}
              }
          });
      } this.ngxService.stop();
  }
  
  //function to download report as excel
  exportexcel()
  {
   /* pass here the table id */
   let element = document.getElementById('report-table');
   const ws: XLSX.WorkSheet =XLSX.utils.table_to_sheet(element);
  
   /* generate workbook and add the worksheet */
   const wb: XLSX.WorkBook = XLSX.utils.book_new();
   XLSX.utils.book_append_sheet(wb, ws, "Sheet1");
  
   /* save to file */  
   XLSX.writeFile(wb, this.fileName);
  }
  
  }//end ofcandidatereport component
  
